import React, { useContext } from 'react';
import LoginPortal from './pages/Login Page/loginPage.js';
import CreateAccount from './pages/Create Account/temp';
import ForgotPassword from './pages/Forgot Password/ForgotComponent';
import ErrorPage from './pages/Error Page/ErrorPage.js';
import { Provider } from 'react-redux';
import { store, persistor } from './redux/store';
import {
  Switch,
  Route,
} from 'react-router-dom';
import { PersistGate } from 'redux-persist/integration/react';
import { UserStateProvider,UserStateContext } from './features/States/UserStates.js';
import MainPage from './pages/Home Page/main.js';

export default function App() {

 
  return (<>
    <Switch>
      <Provider store={store}>
        <PersistGate loading={null} persistor={persistor}>
          <UserStateProvider>
            <Route exact path='/' component={LoginPortal} />
            <Route exact path='/createAccount' component={CreateAccount} />
            <Route exact path='/homePage' component={MainPage} />
            <Route exact path='/forgotPassword' component={ForgotPassword} />
            <Route exact path='/errorPage' component={ErrorPage} />
          </UserStateProvider>
        </PersistGate>
      </Provider>
    </Switch>
  </>);
}