import React, { useContext, useEffect } from 'react';
import Button from '@mui/material/Button';
import { Dialog, TextField, Stack, Divider, Typography, FormControl, } from '@mui/material';
import { connect } from 'react-redux';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import { GlobalStateContext } from '../../features/mainState';
import { Box } from '@mui/system';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import DatePicker from '@mui/lab/DatePicker';
import Autocomplete from '@mui/material/Autocomplete';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import { styled } from '@mui/material/styles';
import ConferenceStepper from '../Stepper/stepperConference'; //import stpperJC
import ConferencePreview from '../Preview/conferencePreview'; //create Journal Preview
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import FormGroup from '@mui/material/FormGroup';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import InputLabel from '@mui/material/InputLabel';


const Input = styled('input')({
  display: 'none',
});


function FormContent(props) {
  const { conferenceFormData, conferenceFormFile, conferenceForm, fetchUsersList } = useContext(GlobalStateContext);

  useEffect(async () => {
    if (props.usersList.length === 0) {
      await fetchUsersList();
    }
  }, []);



  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', justifyContent: 'start', height: 500 }} spacing={2}>
      <Stack direction="row" spacing={10}>
        <Autocomplete
          fullWidth
          sx={{  minWidth: '100%', maxWidth: '100%'  }}
          multiple
          id="tags-outlined"
          options={props.usersList}
          required
          getOptionLabel={(option) => String(option.name + ` (${option.role})`)}
          onChange={(event, value) => conferenceFormData[1]({ ...conferenceFormData[0], authors: value })}
          filterSelectedOptions
          value={conferenceFormData[0]['authors']}
          renderInput={(params) => (
            <TextField
              {...params}
              label="Name of Author*"
            />
          )}
        />
      </Stack>
      <Stack direction="row" spacing={10} marginTop={1}>
      <TextField required id="title" name="title" label="Title" placeholder="Title" variant="outlined" sx={{ '& > :not(style)': { width: '25ch' } }} value={conferenceFormData[0]['title']} onChange={(e) => conferenceFormData[1]({ ...conferenceFormData[0], title: e.target.value })} />
      <TextField required id="conferenceName" name="conferenceName" label="Conference Name" placeholder="Conference Name" variant="outlined" sx={{ '& > :not(style)': { width: '25ch' } }} value={conferenceFormData[0]['conferenceName']} onChange={(e) => conferenceFormData[1]({ ...conferenceFormData[0], conferenceName: e.target.value })} />
        <TextField required id="organizer" name="organizer" label="Organizer"  placeholder="Conference Organizer" variant="outlined" sx={{ '& > :not(style)': { width: '25ch' } }} value={conferenceFormData[0]['organizer']} onChange={(e) => conferenceFormData[1]({ ...conferenceFormData[0], organizer: e.target.value })} />
      </Stack>
      <Stack direction="row" spacing={10} marginTop={1}>
      <FormControl>
          <InputLabel id="demo-simple-select-label">Conference Type</InputLabel>
          <Select
            label="Conference Type*"
            id="Type"
            name="Type"
            required
            placeholder="Conference Type"
            value={conferenceFormData[0]['conferenceType']} onChange={(e) => conferenceFormData[1]({ ...conferenceFormData[0], conferenceType: e.target.value })}
            sx={{ width: '25ch' }}>
            <MenuItem value="National">National</MenuItem>
            <MenuItem value="International">International</MenuItem>
          </Select>
        </FormControl>
        <FormControl >
          <InputLabel id="demo-simple-select-label">Conference Status</InputLabel>
          <Select
            name="Status"
            id="Status"
            label="Conference Status"
            placeholder="Conference Status"
            required
            value={conferenceFormData[0]['paperStatus']} onChange={(e) => conferenceFormData[1]({ ...conferenceFormData[0], paperStatus: e.target.value })} sx={{ width: '25ch' }}>
            <MenuItem value="Approved">Approved</MenuItem>
            <MenuItem value="Pending">Pending</MenuItem>
          </Select>
        </FormControl>
        <TextField id="issueNo" name="issueNo" label="ISSUE NO" variant="outlined" placeholder="ISSUE NO" sx={{ '& > :not(style)': { width: '25ch' } }} value={conferenceFormData[0]['issueNo']} onChange={(e) => conferenceFormData[1]({ ...conferenceFormData[0], issueNo: e.target.value })} />
      </Stack>
      <Stack  direction="row" spacing={10} marginTop={1}>
      <TextField id="isbnNo" name="isbnNo" label="ISBN No" variant="outlined" placeholder="ISBN No" sx={{ '& > :not(style)': { width: '25ch' } }} value={conferenceFormData[0]['isbnNo']} onChange={(e) => conferenceFormData[1]({ ...conferenceFormData[0], isbnNo: e.target.value })} />
      <TextField required id="articleWebLink" name="articleWebLink" label="Conference Web Link" variant="outlined" sx={{ '& > :not(style)': { width: '25ch' } }} value={conferenceFormData[0]['articleWebLink']} onChange={(e) => conferenceFormData[1]({ ...conferenceFormData[0], articleWebLink: e.target.value })} />
      <TextField id="doi" name="doi" label="DOI" variant="outlined" sx={{ '& > :not(style)': { width: '25ch' } }} value={conferenceFormData[0]['doi']} onChange={(e) => conferenceFormData[1]({ ...conferenceFormData[0], doi: e.target.value })} />
      </Stack>
      <Stack direction="row" spacing={6} marginTop={1}>
      <LocalizationProvider dateAdapter={AdapterDateFns}>
            <DatePicker
              views={['year', 'month']}
              label="Year and Month*"
              defaultValue=""
              required
              minDate={new Date('2012-03-01')}
              maxDate={new Date('2023-06-01')}
              value={conferenceFormData[0]['yearAndMonth']} onChange={(newValue) => conferenceFormData[1]({ ...conferenceFormData[0], yearAndMonth: newValue })} name="yearAndMonth" id="yearAndMonth"
              renderInput={(params) => <TextField {...params} />}
            />
          </LocalizationProvider>
          <TextField id="volumeNo" name="volumeNo" label="Volume No" variant="outlined" sx={{ '& > :not(style)': { width: '25ch' } }} value={conferenceFormData[0]['volumeNo']} onChange={(e) => conferenceFormData[1]({ ...conferenceFormData[0], volumeNo: e.target.value })} />
          <label htmlFor="contained-button-file">
            <Input type="file" accept="application/pdf" id="contained-button-file" onChange={(e) => conferenceFormFile[1](e.target.files[0])} />
            <Button variant="contained" component="span" > Upload File</Button>
          </label>
          <Stack direaction="column">
        <Typography variant='body1' fontSize={15}>File Name</Typography>
        <Typography>{conferenceFormFile[0]?.name}</Typography>
        </Stack>
      </Stack>
    
      <Stack  direction="row" spacing={10} marginTop={1}>
      <FormGroup>
      <FormControlLabel control={<Checkbox checked={conferenceFormData[0]['isMMDUAffilation']} onChange={(e) => conferenceFormData[1]({ ...conferenceFormData[0], isMMDUAffilation: e.target.value })} />} id="isMMDUAffilation" name="isMMDUAffilation" label="MMDU Affliated" />
      <FormControlLabel control={<Checkbox checked={conferenceFormData[0]['scopusIndexed']} onChange={(e) => conferenceFormData[1]({ ...conferenceFormData[0], scopusIndexed: e.target.value })} />} id="scopusIndexed" name="scopusIndexed" label="Scopus Indexed" />
      <FormControlLabel control={<Checkbox checked={conferenceFormData[0]['belongsToMMDUStudentsWork']} onChange={(e) => conferenceFormData[1]({ ...conferenceFormData[0], belongsToMMDUStudentsWork: e.target.value })} />} label="mmduStudentWork" name="belongsToMMDUStudentsWork" />
      <FormControlLabel control={<Checkbox checked={conferenceFormData[0]['areYouFirstAuthor']} onChange={(e) => conferenceFormData[1]({ ...conferenceFormData[0], areYouFirstAuthor: e.target.value })} />} label="Are you first Author?" id="areYouFirstAuthor" name="areYouFirstAuthor" />
        </FormGroup>  
       <FormControl>
       <FormControlLabel control={<Checkbox checked={conferenceFormData[0]['indexCoperificus']} onChange={(e) => conferenceFormData[1]({ ...conferenceFormData[0], indexCoperificus: e.target.value })} />} label="Index copernicus" name="indexCopernicus" id="indexCopernicus" />
       <FormControlLabel control={<Checkbox checked={conferenceFormData[0]['isArticleInUGCJournalList']} onChange={(e) => conferenceFormData[1]({ ...conferenceFormData[0], isArticleInUGCJournalList: e.target.value })} />} label="UGC List" name="isArticleInUGCJournalList" id="isArticleInUGCJournalList" />
       <FormControlLabel control={<Checkbox checked={conferenceFormData[0]['correspondingAuthor']} onChange={(e) => conferenceFormData[1]({ ...conferenceFormData[0], correspondingAuthor: e.target.value })} />} label="Corresponding Author" name="correspondingAuthor" id="correspondingAuthor" />
       <FormControlLabel control={<Checkbox checked={conferenceFormData[0]['sci_scie_ssci_indexed']} onChange={(e) => conferenceFormData[1]({ ...conferenceFormData[0], sci_scie_ssci_indexed: e.target.value })} />} label="SCI/SCIE/SSCI Indexed" name="sci_scie_ssci_indexed" id="sci_scie_ssci_indexed" />
        </FormControl>
        <FormControl>
        <FormControlLabel control={<Checkbox checked={conferenceFormData[0]['guideOfAnyCoAuthor']} onChange={(e) => conferenceFormData[1]({ ...conferenceFormData[0], guideOfAnyCoAuthor: e.target.value })} />} label="Guide of Co-Author" name="guideOfAnyCoAuthor" id="guideOfAnyCoAuthor" />
        <FormControlLabel control={<Checkbox checked={conferenceFormData[0]['webOfScienceDatabase']} onChange={(e) => conferenceFormData[1]({ ...conferenceFormData[0], webOfScienceDatabase: e.target.value })} />} label="Web of science database" name="webOfScienceDatabase" id="webOfScienceDatabase" />
        <FormControlLabel control={<Checkbox checked={conferenceFormData[0]['isArticleInPubMedList']} onChange={(e) => conferenceFormData[1]({ ...conferenceFormData[0], isArticleInPubMedList: e.target.value })} />} label="PubMed List" name="isArticleInPubMedList" id="isArticleInPubMedList" />
        <FormControlLabel control={<Checkbox checked={conferenceFormData[0]['googleScholarIndexed']} onChange={(e) => conferenceFormData[1]({ ...conferenceFormData[0], googleScholarIndexed: e.target.value })} />} label="Google Scholar Indexed" name="googleScholarIndexed" id="googleScholarIndexed" />
        </FormControl>
      </Stack>
    </Box>

  );
}


function ConferenceForm(props) {
  const { conferenceForm, stepperActiveStep } = useContext(GlobalStateContext);

  const handleClose = () => {
    conferenceForm[1]({
      state: false,
    });
  };

  const descriptionElementRef = React.useRef(null);

  useEffect(() => {
    if (conferenceForm[0]['state']) {
      const { current: descriptionElement } = descriptionElementRef;
      if (descriptionElement !== null) {
        descriptionElement.focus();
      }
    }
  }, [conferenceForm[0]['state']]);

  return (

    <Box sx={{ justifyContent: 'center' }}>
      <Dialog
        open={conferenceForm[0]['state']}
        onClose={handleClose}
        scroll={'body'}
        aria-labelledby="scroll-dialog-title"
        aria-describedby="scroll-dialog-description"
        fullWidth
        maxWidth="md"
      >
        <DialogTitle id="scroll-dialog-title">Conference Type</DialogTitle>
        <Divider />
        <DialogContent>
          <Box sx={{ width: '100%', mt: 3 }}>
            <ConferenceStepper>
              <Box sx={{ mt: 5, mb: 5, justifyContent: 'center', width: '100%', backgroundColor: 'white' }}>
                {stepperActiveStep[0]['stepperState'] === 0 && <FormContent usersList={props.usersList} />}
                {stepperActiveStep[0]['stepperState'] === 1 && <ConferencePreview />}
                {stepperActiveStep[0]['stepperState'] === 2 &&
                  <Box sx={{ ml: 7 }}>
                    <Typography sx={{ fontWeight: 'bold' }}>Are you sure you want to submit your form?</Typography>
                  </Box>}
              </Box>
            </ConferenceStepper>
          </Box>
        </DialogContent>
        <Divider />
      </Dialog>
    </Box>
  );
}


const mapToProps = (state) => {
  const { usersList } = state;
  return { usersList: usersList };
}

export default connect(mapToProps)(ConferenceForm);

