import React, {useState,createContext, useEffect} from 'react';
import {getJournalPaper, getConferencePaper ,  addJournalConference, updateJournalConference} from '../../api/main';
import { store } from '../../redux/store'; 
import {useDispatch } from 'react-redux';


const GlobalJCStateContext = createContext();

const months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];


function GlobalJCStateProvider({children}){
    const dispatch = useDispatch();

    const [openDialog, setOpenDialog] = useState(false);
    const [formProof, setFormProof] = useState(null);

    const [opr, setOpr] = useState('add');

    // form stepper state
    const [activeStep, setActiveStep] = useState({
        state : 0, 
        disableButton : true
    });

    // open form state
    const [openForm, setOpenForm] = useState({
        state : false, 
        type : 'Journal'
    });

    // form field values
    const [formState, setFormState] = useState({
        authors : [], 
        doi:"",
        manuscriptTitle:"",
        fullJournalName:"",
        publisher:"",
        type:"",
        status:"",
        monthAndYear:null,
        e_issn:"",
        p_issn:"",
        isMMDUAffilation:false,
        scopusIndexed:false,
        belongsToMMDUStudentsWork:false,
        areYouFirstAuthor:false,
        sci_scie_ssci_indexed:false,
        webOfScienceDatabase:false,
        correspondingAuthor:false,
        guideOfAnyCoAuthor:false,
        googleScholarIndexed:false,
        isArticleInPubMedList:false,
        indexCoperificus:false,
        isArticleInUGCJournalList:false,
        articleFrontPage:"",
        articleWebLink:"",
        title:"",
        conferenceName:"",
        organizer:"",
        volumeNo:"",
        issueNo:"",
        isbnNo: "",
        _id : "", 
        usersIds : []
    });

    const [previewMode, setPreviewMode] = useState(false);


    // use effect hook for validating form values
    useEffect(() => {
        if(openForm['type'] === 'Journal'){
            if(formState['authors'].length >0 && formState['manuscriptTitle']!=="" && formState['fullJournalName']!=="" && formState["publisher"]!=="" && formState["e_issn"]!=="" && formState["p_issn"]!=="" && formState["articleWebLink"]!=="" && formState["doi"]!=""){
                setActiveStep({...activeStep, disableButton : false});
            } else {
                setActiveStep({...activeStep, disableButton : true});
            }
            // console.log(formState);
        } else {
            if(formState['authors'].length > 0 && formState['yearAndMonth'] !== null &&formState['title'] !== "" && formState['conferenceName']  !== "" && formState['organizer'] !== "" && formState['issueNo']  !== "" && formProof !== null){
                setActiveStep({...activeStep, disableButton : false});
            } else {
                setActiveStep({...activeStep, disableButton : true});
            }
        }
        
    },[formState, formProof]);

    // preview state
    useEffect(() => {
        if(activeStep['state'] === 1){
            setPreviewMode(true);
        } else {
            setPreviewMode(false);
        }
    }, [activeStep]);


    // useEffect to initialize state

    useEffect(() => {
        if(openDialog === false){
            setOpenForm({
                state : false,
                type : 'Journal'
            });
        }
    }, [openDialog]);

    useEffect(() => {
        if(openForm['state'] === false){
            setOpr('add');
            setFormState({
                authors : [], 
                doi:"",
                manuscriptTitle:"",
                fullJournalName:"",
                publisher:"",
                type:"",
                status:"",
                monthAndYear:null,
                e_issn:"",
                p_issn:"",
                isMMDUAffilation:false,
                scopusIndexed:false,
                belongsToMMDUStudentsWork:false,
                areYouFirstAuthor:false,
                sci_scie_ssci_indexed:false,
                webOfScienceDatabase:false,
                correspondingAuthor:false,
                guideOfAnyCoAuthor:false,
                googleScholarIndexed:false,
                isArticleInPubMedList:false,
                indexCoperificus:false,
                isArticleInUGCJournalList:false,
                articleFrontPage:"",
                articleWebLink:"",
                title:"",
                conferenceName:"",
                organizer:"",
                volumeNo:"",
                issueNo:"",
                _id : "",
                isbnNo: "",
                usersIds : []
            });
            setPreviewMode(false);
            setFormProof(null);
            setActiveStep({
                state : 0, 
                disableButton : true
            });
        }
    }, [openForm]);

    // set initial state functions

    const handleChange = (name, value) => {

        setFormState({
            ...formState,
            [`${name}`] : value
        });
        
    };

    const handleGetJournalPaper = async() => {
        const response = await getJournalPaper();
        await store.dispatch({
            type : 'storeJournalPapers', 
            payload : response
        });
    }

    const handleGetConferencePaper = async() => {
        const response = await getConferencePaper();
        await store.dispatch({
            type : 'storeConferencePapers', 
            payload : response
        });
    }

    // handle submit for form
    const handleSubmitForm = async () =>{
        let data = new FormData();

        data.append('authors',JSON.stringify(formState['authors']));
        data.append('doi',formState['doi']);
        data.append('articleWebLink',formState['articleWebLink']);
        data.append('isMMDUAffilation',formState['isMMDUAffilation']);
        data.append('belongsToMMDUStudentsWork',formState['belongsToMMDUStudentsWork']);
        data.append('correspondingAuthor',formState['correspondingAuthor']);
        data.append('guideOfAnyCoAuthor',formState['guideOfAnyCoAuthor']);
        data.append('isArticleInPubMedList',formState['isArticleInPubMedList']);
        data.append('indexCoperificus',formState['indexCoperificus']);
        data.append('scopusIndexed',formState['scopusIndexed']);
        data.append('areYouFirstAuthor',formState['areYouFirstAuthor']);
        data.append('sci_scie_ssci_indexed',formState['sci_scie_ssci_indexed']);
        data.append('webOfScienceDatabase',formState['webOfScienceDatabase']);
        data.append('googleScholarIndexed',formState['googleScholarIndexed']);
        data.append('isArticleInUGCJournalList',formState['isArticleInUGCJournalList']);
        data.append('articleFrontPage',formProof);

        if(openForm['type'] === 'Journal'){
            data.append('manuscriptTitle',formState['manuscriptTitle']);
            data.append('fullJournalName',formState['fullJournalName']);
            data.append('publisher',formState['publisher']);
            data.append('publicationType',formState['type']);
            data.append('publicationStatus',formState['status']);
            data.append('e_issn',formState['e_issn']);
            data.append('p_issn',formState['p_issn']);    
        }

        if(openForm['type'] === 'Conference'){
            data.append('title',formState['title']);
            data.append('conferenceName',formState['conferenceName']);
            data.append('organizer',formState['organizer']);
            data.append('conferenceType',formState['type']);
            data.append('paperStatus',formState['status']);
            data.append('volumeNo',formState['volumeNo']);
            data.append('issueNo',formState['issueNo']);
            data.append('isbnNo',formState['isbnNo']);
        }

        if(opr === 'update'){
            data.append('_id', formState['_id']);
            console.log(formState.monthAndYear);
            if(formState['monthAndYear']?.getMonth){
                data.append('monthAndYear',String(months[formState['monthAndYear']?.getMonth()] + " " + formState['monthAndYear']?.getFullYear()));
                
            } else {
                data.append('monthAndYear', formState['monthAndYear']);
            }
        } else {
            data.append('monthAndYear',String(months[formState['monthAndYear']?.getMonth()] + " " + formState['monthAndYear']?.getFullYear()));
        }

        // console.log(`opr : ${opr}`, ` type : ${openForm.type}`);

        // console.log(formState);

        let response;

        if(opr === 'add'){
            response = await addJournalConference(openForm['type'], data);
        } else {
            response = await updateJournalConference(openForm['type'], data);
        }

        alert(` Message : ${response.message}`);

        if(openForm['type'] === "Journal"){
            await handleGetJournalPaper();
            
        }
        if(openForm['type'] === "Conference"){
            await handleGetConferencePaper();
        }

        setOpenDialog(false);
    }

    const finalObject = {
        openDialogJC : openDialog, 
        setOpenDialogJC : setOpenDialog,
        formProof : formProof,
        setFormProof : setFormProof, 
        activeStep : activeStep,
        setActiveStep : setActiveStep, 
        openForm : openForm, 
        setOpenForm : setOpenForm,
        formState : formState,
        setFormState : setFormState, 
        handleSubmitForm : handleSubmitForm, 
        handleChange : handleChange, 
        preview : previewMode, 
        opr : opr, 
        setOpr : setOpr
    };

    return (
        <GlobalJCStateContext.Provider value={finalObject}>
            {children}
        </GlobalJCStateContext.Provider>
    );
    
}

export {GlobalJCStateContext, GlobalJCStateProvider};
