import React, { useState, useContext } from 'react';
import { Stack, List, ListItem, ListItemIcon, FormControl, InputLabel, Select, MenuItem, FormControlLabel, Checkbox } from '@mui/material';
import DatePickerComponent from '../../components/DatePicker/datepicker';
import CheckBox from '../../components/CheckBox/checkbox';
import SelectComponent from '../Select/select';
import { GlobalJCDrawerContext } from '../../features/States/JCDrawerState';

export default function DrawerJournalConference() {


  const { state, handleChange } = useContext(GlobalJCDrawerContext);

  return (
    <List>
      <DatePickerComponent from={state['from']} handleChange={handleChange} to={state['to']} />
      <CheckBox />
      <SelectComponent
        state={state}
        handleChange={handleChange}
      />
    </List>
  );
}