import React, { useContext, useEffect } from 'react';
import Button from '@mui/material/Button';
import { Dialog, TextField, Stack, Divider, Typography, FormControl, } from '@mui/material';
import { connect } from 'react-redux';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import { GlobalStateContext } from '../../features/mainState';
import { Box } from '@mui/system';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import DatePicker from '@mui/lab/DatePicker';
import Autocomplete from '@mui/material/Autocomplete';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import { styled } from '@mui/material/styles';
import MyStepper from '../Stepper/stepper';
import PatentPreview from '../Preview/patentPreview';
import InputLabel from '@mui/material/InputLabel';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import { GlobalPCStateContext } from '../../features/States/PatentStates';



const Input = styled('input')({
  display: 'none',
});

function FormContent(props) {

  const {setFormProof, openForm,  preview} = useContext(GlobalPCStateContext);

  const {nameOfInventors, setNameOfInventors, title , setTitle, applicationNo, setApplicationNo, monthAndYear, setMonthAndYear, status, setStatus, nameOfAgency, setNameOfAgency} = useContext(GlobalPCStateContext);

  const {fetchUsersList} = useContext(GlobalStateContext);

  useEffect(async () => {
    if (props.usersList.length === 0) {
      await fetchUsersList();
    }
  }, []);

  const status1 = openForm['type'] === 'Patent' ? ['Applied', 'Published', 'Granted'] : ['Applied', 'Published', 'Registered'];


  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', justifyContent: 'start', bgcolor : 'white'}} spacing={2}>
      <Stack direction='row' spacing={10} sx={{ flexGrow: -1, ml: 5, mr: 5, mb: 1, mt: 1 }}>
        <Autocomplete
          fullWidth
          disabled={preview}
          sx={{ minWidth: '100%', maxWidth: '100%' }}
          multiple
          id="tags-outlined"
          options={props.usersList}
          disableListWrap={false}
          required
          getOptionLabel={(option) => String(option.name)}
          onChange={(event, value) => setNameOfInventors(value)}
          filterSelectedOptions
          value={nameOfInventors}
          name="nameOfInventors"
          renderInput={(params) => (
            <TextField
              {...params}
              label="Name of Inventors"
            />
          )}
        />
      </Stack>
      <Stack direction='row' spacing={10} sx={{ flexGrow: -1, ml: 5, mr: 5, mb: 1, mt: 1 }}>
        <TextField disabled={preview} fullWidth sx={{ minWidth: '45%', maxWidth: '45%' }} id="outlined-basic" label="Title" variant="outlined" value={title} name="title" onChange={(e) => setTitle(e.target.value )} />
        <TextField disabled={preview} fullWidth sx={{ minWidth: '45%', maxWidth: '45%' }} id="outlined-basic" label="Name of Agency" variant="outlined" value={nameOfAgency} onChange={(e) => setNameOfAgency(e.target.value)} name="nameOfAgency" />
      </Stack>
      <LocalizationProvider dateAdapter={AdapterDateFns}>
        <Stack direction='row' spacing={10} sx={{ flexGrow: -1, ml: 5, mr: 5, mb: 1, mt: 1 }} >
          <FormControl fullWidth sx={{ minWidth: '45%', maxWidth: '45%' }}>
            <InputLabel id="demo-simple-select-label">Status</InputLabel>
            <Select
           id="outlined-select-currency-native"
           select
           required
           label="Status*"
           value={status}
           disabled={preview}
           onChange={(e) => setStatus(e.target.value)}
            >
               {status1.map((item) => (
              <MenuItem key={item} value={item}>{item}</MenuItem>
            ))}              
            </Select>
          </FormControl>
          <DatePicker
            views={['month', 'year']}
            label="Month and Year"
            minDate={new Date('2012-03-01')}
            defaultValue=""
            required
            maxDate={new Date('2023-06-01')}
            value={monthAndYear}
            disabled={preview}
            onChange={(newValue) => {
              setMonthAndYear(newValue);
            }}
            renderInput={(params) => <TextField fullWidth sx={{ maxWidth: '45%', minWidth: '45%' }} {...params} />}
          />
        </Stack>
      </LocalizationProvider>
      <Stack direction='row' spacing={10} sx={{ flexGrow: -1, ml: 5, mr: 5, mb: 1, mt: 1 }}>
        <TextField disabled={preview} fullWidth sx={{ minWidth: '45%', maxWidth: '45%' }} id="outlined-basic" label="Application No." type={'number'} variant="outlined" value={applicationNo} onChange={(e) => setApplicationNo(e.target.value)} />
        <label htmlFor="contained-button-file" >
          <Input type="file" accept="application/pdf" id="contained-button-file" onChange={(e) => setFormProof(e.target.files[0])} />
          <Button disabled={preview} fullWidth sx={{ mt: 2, ml: 8, alignSelf: 'center' }} variant="contained" component="span"> Upload Proof</Button>
        </label>
      </Stack>
    </Box>
  );
}





function PatentForm(props) {

  const {openForm, setActiveStep, activeStep, handleSubmitForm, setOpenForm} = useContext(GlobalPCStateContext);

  const handleClose1 = () => {
    setOpenForm({
      state : false, 
      type : ""
    });
  }

  const descriptionElementRef = React.useRef(null);
  
  useEffect(() => {
    if (openForm['state']) {
      const { current: descriptionElement } = descriptionElementRef;
      if (descriptionElement !== null) {
        descriptionElement.focus();
      }
    }
  }, [openForm['state']]);
  
  return (
    <Box sx={{ justifyContent: 'center' }}>
      <Dialog
        open={openForm['state']}
        onClose={handleClose1}
        scroll={'body'}
        aria-labelledby="scroll-dialog-title"
        aria-describedby="scroll-dialog-description"
        fullWidth
        maxWidth="md"
      >
        <DialogTitle id="scroll-dialog-title">
          <Typography variant="h5" gutterBottom >{openForm['type']}</Typography>
        </DialogTitle>
        <Divider />
        <DialogContent>
          <Box sx={{ width: '100%', mt: 3 }}>
            <MyStepper setActiveStep={setActiveStep} disableButton={activeStep['disableButton']} handleSubmit={handleSubmitForm} activeStep={activeStep}>
              <Box sx={{ mt: 5, mb: 5, justifyContent: 'center', width: '100%', backgroundColor: 'white' }}>
                {(activeStep['state'] === 0 || activeStep['state'] === 1) && <FormContent usersList={props.usersList} />}
                {activeStep['state'] === 2 &&
                  <Box sx={{ ml: 7 }}>
                    <Typography sx={{ fontWeight: 'bold' }}>Are you sure you want to submit your form?</Typography>
                  </Box>}
              </Box>
            </MyStepper>
          </Box>
        </DialogContent>
        <Divider />
      </Dialog>
    </Box>
  );

  
}

const mapToProps = (state) => {
  const { usersList } = state;
  return { usersList: usersList };
}

export default connect(mapToProps)(PatentForm);