import React, { useContext } from 'react';
import { Box, Typography, Stack } from '@mui/material';
import { GlobalStateContext } from '../../features/mainState';

const months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];

export default function ConferencePreview() {
    const { conferenceFormData, conferenceFormFile } = useContext(GlobalStateContext);
    return (
        <Box sx={{ display: 'flex', flexDirection: 'column', mb: -2, ml: 5, mr: 5, justifyContent: 'start', alignSelf: 'center', width: 800, backgroundColor: 'white' }} >
            <Stack direction={'column'} spacing={4} sx={{ ml: 5, mr: 5 }}>
                <Stack direction={'column'}>
                    <Typography variant='body1' fontWeight={600} fontSize={15}>Name of Autors</Typography>
                    <ul display='flex' justify-content='space-around' list-style-type='none'>
                        {conferenceFormData[0]['authors'].map((items) => (
                            <li>
                                <Typography noWrap sx={{ width: 700, backgroundColor: 'white' }}>{items.name}</Typography>
                            </li>
                        ))}
                    </ul>
                </Stack>
                <Stack direction={'row'} spacing={12}>
                    <Stack direction={'column'}>
                        <Typography variant='body1' fontWeight={600} fontSize={15}>Conference Title</Typography>
                        <Typography noWrap sx={{ width: 200, backgroundColor: 'white' }}>{conferenceFormData[0]['title']}</Typography>
                    </Stack>
                    <Stack direction={'column'}>
                        <Typography variant='body1' fontWeight={600} fontSize={15}>Conference Name</Typography>
                        <Typography noWrap sx={{ width: 100, backgroundColor: 'white' }}>{conferenceFormData[0]['conferenceName']}</Typography>
                    </Stack>
                    <Stack direction={'column'}>
                        <Typography variant='body1' fontWeight={600} fontSize={15}>Publication Name</Typography>
                        <Typography noWrap sx={{ width: 200, backgroundColor: 'white' }}>{conferenceFormData[0]['organizer']}</Typography>
                    </Stack>
                </Stack>
                <Stack direction={'row'} spacing={15}>
                    <Stack direction={'column'}>
                        <Typography variant='body1' fontWeight={600} fontSize={15}>Conference Type</Typography>
                        <Typography noWrap sx={{ width: 150, backgroundColor: 'white' }}>{conferenceFormData[0]['conferenceType']}</Typography>
                    </Stack>
                    <Stack direction={'column'}>
                        <Typography variant='body1' fontWeight={600} fontSize={15}>Conference Status</Typography>
                        <Typography noWrap sx={{ width: 150, backgroundColor: 'white' }}>{conferenceFormData[0]['paperStatus']}</Typography>
                    </Stack>
                    <Stack direction={'column'}>
                        <Typography variant='body1' fontWeight={600} fontSize={15}>Year And Month</Typography>
                        <Typography noWrap sx={{ width: 150, backgroundColor: 'white' }}>{String(months[conferenceFormData[0]['yearAndMonth']?.getMonth()] + " " + conferenceFormData[0]['yearAndMonth']?.getFullYear())}</Typography>
                    </Stack>
                </Stack>

                <Stack direction={'row'} spacing={15}>
                    <Stack direction={'column'}>
                        <Typography variant='body1' fontWeight={600} fontSize={15}>{ `ISSUE-No`}</Typography>
                        <Typography noWrap sx={{ width: 150, backgroundColor: 'white' }}>{conferenceFormData[0]['issueNo']}</Typography>
                    </Stack>
                    <Stack direction={'column'}>
                        <Typography variant='body1' fontWeight={600} fontSize={15}>{`ISBN-No`}</Typography>
                        <Typography noWrap sx={{ width: 150, backgroundColor: 'white' }}>{conferenceFormData[0]['isbnNo']}</Typography>
                    </Stack>
                    <Stack direction={'column'}>
                        <Typography variant='body1' fontWeight={600} fontSize={15}>Volume Number</Typography>
                        <Typography noWrap sx={{ width: 150, backgroundColor: 'white' }}>{conferenceFormData[0]['volumeNo']}</Typography>
                    </Stack>    
                </Stack>

                <Stack direction={'row'} spacing={15}>
                    <Stack direction={'column'}>
                        <Typography variant='body1' fontWeight={600} fontSize={15}>Article Web Link</Typography>
                        <Typography noWrap sx={{ width: 150, backgroundColor: 'white' }}>{conferenceFormData[0]['articleWebLink']}</Typography>
                    </Stack>
                    <Stack direction={'column'}>
                        <Typography variant='body1' fontWeight={600} fontSize={15}>Article DOI</Typography>
                        <Typography noWrap sx={{ width: 150, backgroundColor: 'white' }}>{conferenceFormData[0]['doi']}</Typography>
                    </Stack>
                    <Stack direction={'column'}>
                        <Typography variant='body1' fontWeight={600} fontSize={15}>File Name</Typography>
                        <Typography noWrap sx={{ width: 150, backgroundColor: 'white' }}>{conferenceFormFile[0]?.name}</Typography>
                    </Stack>
                </Stack>

                <Stack direction={'row'} spacing={15}>
                    <Stack direction={'column'}>
                        <Typography variant='body1' fontWeight={600} fontSize={15}>MMDU Affliated</Typography>
                        <Typography noWrap sx={{ width: 150, backgroundColor: 'white' }}>{conferenceFormData[0]['isMMDUAffilation']=="on"?"Selected":"Not Selected"}</Typography>
                    </Stack>
                    <Stack direction={'column'}>
                        <Typography variant='body1' fontWeight={600} fontSize={15}>Scopus Indexed</Typography>
                        <Typography noWrap sx={{ width: 150, backgroundColor: 'white' }}>{conferenceFormData[0]['scopusIndexed']=="on"?"Selected":"Not Selected"}</Typography>
                    </Stack>
                    <Stack direction={'column'}>
                        <Typography variant='body1' fontWeight={600} fontSize={15}>MMDU StudentWork</Typography>
                        <Typography noWrap sx={{ width: 150, backgroundColor: 'white' }}>{conferenceFormData[0]['belongsToMMDUStudentsWork']=="on"?"Selected":"Not Selected"}</Typography>
                    </Stack>
                </Stack>

                <Stack direction={'row'} spacing={15}>
                    <Stack direction={'column'}>
                        <Typography variant='body1' fontWeight={600} fontSize={15}>Are you first Author?</Typography>
                        <Typography noWrap sx={{ width: 150, backgroundColor: 'white' }}>{conferenceFormData[0]['areYouFirstAuthor']=="on"?"Selected":"Not Selected"}</Typography>
                    </Stack>
                    <Stack direction={'column'}>
                        <Typography variant='body1' fontWeight={600} fontSize={15}>Corresponding Author</Typography>
                        <Typography noWrap sx={{ width: 150, backgroundColor: 'white' }}>{conferenceFormData[0]['correspondingAuthor']=="on"?"Selected":"Not Selected"}</Typography>
                    </Stack>
                    <Stack direction={'column'}>
                        <Typography variant='body1' fontWeight={600} fontSize={15}>SCI/SCIE/SSCI Indexed</Typography>
                        <Typography noWrap sx={{ width: 150, backgroundColor: 'white' }}>{conferenceFormData[0]['sci_scie_ssci_indexed']=="on"?"Selected":"Not Selected"}</Typography>
                    </Stack>
                </Stack>

                <Stack direction={'row'} spacing={15}>
                    <Stack direction={'column'}>
                        <Typography variant='body1' fontWeight={600} fontSize={15}>Guide of Co-Author</Typography>
                        <Typography noWrap sx={{ width: 150, backgroundColor: 'white' }}>{conferenceFormData[0]['guideOfAnyCoAuthor']=="on"?"Selected":"Not Selected"}</Typography>
                    </Stack>
                    <Stack direction={'column'}>
                        <Typography variant='body1' fontWeight={600} fontSize={15}>Web of science database</Typography>
                        <Typography noWrap sx={{ width: 150, backgroundColor: 'white' }}>{conferenceFormData[0]['webOfScienceDatabase']=="on"?"Selected":"Not Selected"}</Typography>
                    </Stack>
                    <Stack direction={'column'}>
                        <Typography variant='body1' fontWeight={600} fontSize={15}>PubMed List</Typography>
                        <Typography noWrap sx={{ width: 150, backgroundColor: 'white' }}>{conferenceFormData[0]['isArticleInPubMedList']=="on"?"Selected":"Not Selected"}</Typography>
                    </Stack>
                </Stack>
                <Stack direction={'row'} spacing={15}>
                    <Stack direction={'column'}>
                        <Typography variant='body1' fontWeight={600} fontSize={15}>Google Scholar Indexed</Typography>
                        <Typography noWrap sx={{ width: 150, backgroundColor: 'white' }}>{conferenceFormData[0]['googleScholarIndexed']=="on"?"Selected":"Not Selected"}</Typography>
                    </Stack>
                    <Stack direction={'column'}>
                        <Typography variant='body1' fontWeight={600} fontSize={15}>Index Copernicus</Typography>
                        <Typography noWrap sx={{ width: 150, backgroundColor: 'white' }}>{conferenceFormData[0]['indexCoperificus']=="on"?"Selected":"Not Selected"}</Typography>
                    </Stack>
                    <Stack direction={'column'}>
                        <Typography variant='body1' fontWeight={600} fontSize={15}>UGC List</Typography>
                        <Typography noWrap sx={{ width: 150, backgroundColor: 'white' }}>{conferenceFormData[0]['isArticleInUGCConferenceList']=="on"?"Selected":"Not Selected"}</Typography>
                    </Stack>
                </Stack>
            </Stack>
        </Box>
    );
}