import React, {Fragment, useContext} from 'react';
import Box from '@mui/material/Box';
import {Stepper, Divider} from '@mui/material';
import Step from '@mui/material/Step';
import StepLabel from '@mui/material/StepLabel';
import Button from '@mui/material/Button';
import { GlobalStateContext } from '../../features/mainState';

const steps = ['Fill Form', 'Preview', 'Submit'];

export default function ConferenceStepper({children}) {
  

  const {stepperActiveStep, handleAddConferenceSubmit, conferenceForm  } = useContext(GlobalStateContext);

  const handleNext = () => {
    stepperActiveStep[1]((prevActiveStep) => {return {...prevActiveStep, stepperState : prevActiveStep['stepperState']+1}}); 
  };

  const handleBack = () => {
    stepperActiveStep[1]((prevActiveStep) => {return {...prevActiveStep, stepperState : prevActiveStep['stepperState']-1}});
  };


  const handleReset = () => {
    stepperActiveStep[1](0);
  };

  return (
    <Box sx={{ width: '100%' }}>
      <Stepper activeStep={stepperActiveStep[0]['stepperState']}>
        {steps.map((label, index) => {
          const stepProps = {};
          const labelProps = {};
          return (
            <Step key={label} {...stepProps}>
              <StepLabel {...labelProps}>{label}</StepLabel>
            </Step>
          );
        })}
      </Stepper>
      <Box>
        {children}
      </Box>
      <Divider/>
      <Fragment>
            <Box sx={{ display: 'flex', flexDirection: 'row', pt: 2 }}>
                <Button
                    color="inherit"
                    disabled={stepperActiveStep[0]['stepperState'] === 0}
                    onClick={handleBack}
                    sx={{ mr: 1 }}
                  >
                    Back
                  </Button>
              <Box sx={{ flex: '1 1 auto' }} />
              {
                stepperActiveStep[0]['stepperState'] === 2 ? 
                (<Button onClick={conferenceForm[0]['state'] === true ? handleAddConferenceSubmit : handleReset} >Submit</Button>) : 
                (<Button onClick={handleNext} disabled={stepperActiveStep[0]['nextButton']}>Next</Button>)
              }
            </Box>
          </Fragment>
    </Box>
  );
}
