function renderCell(prams) {
  return (
    <div
      style={{
        fontSize: 13,
        width: "220%",
        textAlign: "center"
      }}
    >
      {prams.value}
    </div>
  );
}

function ActionButton(props) {
  const { state } = useContext(GlobalJCDrawerContext);

  return (
    <Stack direction="row" spacing={1} sx={{ width: '220%', justifyContent: 'center' }}>
      {!state['papertoapprove'] ? (<IconButton onClick={props.onClick}>
        <EditIcon fontSize='small' />
      </IconButton>) : ''}
      <IconButton name="Approve">
        <CheckCircleIcon sx={{ color: 'green' }} />
      </IconButton>
      <IconButton name="Reject">
        <CancelIcon sx={{ color: 'red' }} />
      </IconButton>
    </Stack>
  );
}

function ActionButtonJC(params) {

  const { setFormState, setOpenForm, setOpr, setOpenDialogJC, formState } = useContext(GlobalJCStateContext);

  const { myRole } = useContext(UserStateContext);

  const onClick = async (e) => {
    setOpenDialogJC(true);
    setOpr('update');
    await setFormState({
      authors: params.row.authors,
      doi: params.row.doi,
      monthAndYear: params.row.monthAndYear,
      isMMDUAffilation: (params.row.isMMDUAffilation === "true"),
      scopusIndexed: params.row.scopusIndexed === "true",
      belongsToMMDUStudentsWork: (params.row.belongsToMMDUStudentsWork === "true"),
      areYouFirstAuthor: params.row.areYouFirstAuthor === "true",
      sci_scie_ssci_indexed: params.row.sci_scie_ssci_indexed === "true",
      webOfScienceDatabase: params.row.webOfScienceDatabase === "true",
      correspondingAuthor: params.row.correspondingAuthor === "true",
      guideOfAnyCoAuthor: params.row.guideOfAnyCoAuthor === "true",
      googleScholarIndexed: params.row.googleScholarIndexed === "true",
      isArticleInPubMedList: params.row.isArticleInPubMedList === "true",
      indexCoperificus: params.row.indexCoperificus === "true",
      isArticleInUGCJournalList: params.row.isArticleInUGCJournalList === "true",
      articleFrontPage: params.row.articleFrontPage,
      articleWebLink: params.row.articleWebLink,
      _id: params.row._id,
      manuscriptTitle: params.row?.manuscriptTitle,
      fullJournalName: params.row?.fullJournalName,
      publisher: params.row?.publisher,
      e_issn: params.row?.e_issn,
      p_issn: params.row?.p_issn,
      type: (params.row?.publicationType || params.row?.conferenceType),
      status: (params.row?.publicationStatus || params.row?.paperStatus),
      title: params.row?.title,
      conferenceName: params.row?.conferenceName,
      organizer: params.row?.organizer,
      volumeNo: params.row?.volumeNo,
      issueNo: params.row?.issueNo,
      isbnNo: params.row?.isbnNo,
    });

    if (params.row?.manuscriptTitle) {
      setOpenForm({
        state: true,
        type: 'Journal'
      });
    } else {
      setOpenForm({
        state: true,
        type: 'Conference'
      });
    }
  };

  return (
    <div>
      {myRole === "Faculty" ? (<IconButton onClick={onClick} alignItems="center">
        <EditIcon fontSize='small' />
      </IconButton>) : (<ActionButton onClick={onClick} />)}
    </div>
  )
};

function ActionButtonPatent(params) {

  const { setOpenForm, setOpr, setOpenDialog, setNameOfInventors, setNameOfAgency, setTitle, setApplicationNo, setMonthAndYear, setStatus, set_Id } = useContext(GlobalPCStateContext);

  const onClick = async (e) => {

    setOpenDialog(true);
    setOpr('update');

    setNameOfInventors(params.row.nameOfInventors);
    setTitle(params.row.title);
    setApplicationNo(params.row.applicationNo);
    setMonthAndYear(params.row.monthAndYear);
    setStatus(params.row.status);
    setNameOfAgency(params.row.nameOfAgency);
    set_Id(params.row._id);

    setOpenForm({
      state: true,
      type: 'Patent'
    });
  };

  return (
    <IconButton onClick={onClick}>
      <EditIcon fontSize='small' />
    </IconButton>
  )
};

function ActionButtonCopyright({ params, props }) {

  const { setOpenForm, setOpr, setOpenDialog, set_Id, setNameOfInventors, setNameOfAgency, setTitle, setApplicationNo, setMonthAndYear, setStatus } = useContext(GlobalPCStateContext);

  const onClick = async (e) => {

    setOpenDialog(true);
    setOpr('update');

    await setNameOfInventors(params.row.nameOfInventors);
    await setTitle(params.row.title);
    await setApplicationNo(params.row.applicationNo);
    await setMonthAndYear(params.row.monthAndYear);
    await setStatus(params.row.status);
    await setNameOfAgency(params.row.nameOfAgency);
    set_Id(params.row._id);

    setOpenForm({
      state: true,
      type: 'Copyright'
    });
  };

  return (
    <IconButton onClick={onClick}>
      <EditIcon fontSize='small' />
    </IconButton>
  )
};

function RenderUser(params) {

  let userNames = "";

  if (params.row?.authors) {
    params.row?.authors.forEach((item, index) => {
      userNames += ` ${item.name} `;
      if (index + 1 !== params.row.authors?.length) {
        userNames += ','
      };
    });
  } else {
    params.row?.nameOfInventors.forEach((item, index) => {
      userNames += ` ${item.name} `;
      if (index + 1 !== params.row.nameOfInventors?.length) {
        userNames += ','
      };
    });
  }

  return (
    <Tooltip title={userNames}>
      <Typography noWrap={true} sx={{ fontSize: 13, width: '220%', textAlign: 'center' }}>
        {userNames}
      </Typography>
    </Tooltip>
  );
}

