const PaperDataContext = createContext();


function DataProvider(props) {

  const [journalRow, setJournalRow] = useState([]);
  const [conferenceRow, setConferenceRow] = useState([]);
  const [patentRow, setPatentRow] = useState([]);
  const [copyrightRow, setCopyrightRow] = useState([]);


  const finalObject = {
    journalRow: journalRow,
    journalColumn: journalColumn,
    conferenceRow: conferenceRow,
    conferenceColumn: conferenceColumn,
    patentRow: patentRow,
    patentColumn: patentColumn,
    copyrightColumn: copyrightColumn,
    copyrightRow: copyrightRow
  };

  return (
    <PaperDataContext.Provider value={finalObject}>
      {props.children}
    </PaperDataContext.Provider>
  );
}


function mapStateToProps(state) {
  const { papers, user } = state
  return { user: user, journal: papers['journalPapers'], conference: papers['conferencePapers'], patent: papers['patentPapers'], copyright: papers['copyrightPapers'] };
}

export default {
  DataProvider: connect(mapStateToProps)(DataProvider),
  PaperDataContext: PaperDataContext
}
