import React, {Fragment, useState, useContext} from 'react';
import Box from '@mui/material/Box';
import {Stepper, Divider} from '@mui/material';
import Step from '@mui/material/Step';
import StepLabel from '@mui/material/StepLabel';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import { GlobalStateContext } from '../../features/mainState';

const steps = ['Fill Form', 'Preview', 'Submit'];

export default function MyStepper({children, setActiveStep, activeStep, handleSubmit }) {
  

  const {stepperActiveStep, handleAddPatentCopyrightSubmit, patentForm} = useContext(GlobalStateContext);

  const handleNext = () => {
    stepperActiveStep[1]((prevActiveStep) => {return {...prevActiveStep, stepperState : prevActiveStep['stepperState']+1}}); 
  };

  const handleNext1 = () => {
    setActiveStep((prevState) => {return {...prevState , state : prevState['state'] + 1};});
  };

  const handleBack = () => {
    stepperActiveStep[1]((prevActiveStep) => {return {...prevActiveStep, stepperState : prevActiveStep['stepperState']-1}});
  };
  const handleBack1 = () => {
    setActiveStep((prevState) => {return {...prevState , state : prevState['state'] - 1};});

  }


  const handleReset = () => {
    stepperActiveStep[1](0);
  };

  const handleReset1 = () => {
    setActiveStep({
      state : 0, 
      disableButton : true
    })
  }

  return (
    <Box sx={{ width: '100%' }}>
      <Stepper activeStep={activeStep['state']}>
        {steps.map((label, index) => {
          const stepProps = {};
          const labelProps = {};
          return (
            <Step key={label} {...stepProps}>
              <StepLabel {...labelProps}>{label}</StepLabel>
            </Step>
          );
        })}
      </Stepper>
      <Box>
        {children}
      </Box>
      <Divider/>
      <Fragment>
            <Box sx={{ display: 'flex', flexDirection: 'row', pt: 2 }}>
                <Button
                    color="inherit"
                    disabled={activeStep['state'] === 0}
                    onClick={handleBack1}
                    sx={{ mr: 1 }}
                  >
                    Back
                  </Button>
              <Box sx={{ flex: '1 1 auto' }} />
              {
                activeStep['state'] === 2 ? 
                (<Button onClick={handleSubmit} >Submit</Button>) : 
                (<Button onClick={handleNext1} disabled={activeStep['disableButton']}>Next</Button>)
              }
            </Box>
          </Fragment>
    </Box>
  );
}
