export async function addJournalConference( type, formData) {
    try {
        const response = await axios({
            method : 'post', 
            url : `/${type}/add${type}`,
            withCredentials : true, 
            data : formData, 
        });

        

        return {message : response.data.message};
        
    } catch(error) {
        
        if(error){
            const message = await error.response.data.text();
            return {
                message : message
            };
        }
    }
}

