export async function downloadDocx(type) {
    try {
        await axios({
            method : 'get', 
            url : `/downloadDocx?type=${type}`, 
            withCredentials : true,
            responseType : 'blob', 
            
        }).then((response) => {
            FileDownload(response.data, `${type} Paper Report.docx`);
        });
        return { 
            message : "Successfully Downloaded."
        }
    } catch(error) {
        if(error){
            const message = await error.response.data.text();
            return {
                message : message
            };
        }
    }
}

