import React, { useState, createContext, useEffect } from 'react';
import { logout, addPatentCopyright, getAllPapers, fetchUsers, approveAccount, rejectAccount, addPublications, addJournalConference , GetUnapprovedAccounts, getUnapprovedAccounts} from '../api/main';
import { store } from '../redux/store';
import { useDispatch } from 'react-redux';
import { useHistory } from "react-router-dom";
import logo from '../Images/dp.png';



const GlobalStateContext = createContext();

const months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];



function GlobalStateProvider({children }) {
    const dispatch = useDispatch();
    const history = useHistory();


    //States 

    const [tabStatus, setTabStatus] = useState("JC")
    const [openBackdrop, setOpenBackdrop] = useState(false);
    const [snackbar, setSnackbar] = useState({
        open: false,
        message: ""
    });

    const [unapprovedAccounts, setUnapprovedAccounts] = useState([]);
    const [openDownloadDocxDialog, setOpenDownloadDialog] = useState(false);
    //journal dialog
    const [openJournalDialog, setOpenJournalDialog] = useState(false);
    const [openConferenceDialog, setOpenConfereceDialog] = useState(false);
    const [journalFormProof, setJournalFormProof] = useState(null);
    const [conferenceFormProof, setConferenceFormProof] = useState(null);
    //patent dialog
    const [activeStep, setActiveStep] = useState({
        stepperState: 0,
        nextButton: true
    });

    const [openJournalForm, setOpenJournalForm] = useState({
        state: false,
    });

    const [openConferenceForm, setOpenConferenceForm] = useState({
        state: false,
    });

    const [journalFormState, setJournalFormState] = useState({
        authors: [],
        doi: "",
        manuscriptTitle: "",
        fullJournalName: "",
        publisher: "",
        publicationType: "",
        publicationStatus: "",
        yearAndMonth: null,
        e_issn: "",
        p_issn: "",
        volumeNo: "",
        issueNo: "",
        isMMDUAffilation: false,
        scopusIndexed: false,
        belongsToMMDUStudentsWork: false,
        areYouFirstAuthor: false,
        sci_scie_ssci_indexed: false,
        webOfScienceDatabase: false,
        correspondingAuthor: false,
        guideOfAnyCoAuthor: false,
        googleScholarIndexed: false,
        isArticleInPubMedList: false,
        indexCoperificus: false,
        isArticleInUGCJournalList: false,
        articleFrontPage: "",
        articleWebLink: "",
    });


    const [conferenceFormState, setConferenceFormState] = useState({
        authors: [],
        doi: "",
        title: "",
        conferenceName: "",
        organizer: "",
        conferenceType: "",
        paperStatus: "",
        yearAndMonth: null,
        volumeNo: "",
        issueNo: "",
        isbnNo: "",
        isMMDUAffilation: false,
        scopusIndexed: false,
        belongsToMMDUStudentsWork: false,
        areYouFirstAuthor: false,
        sci_scie_ssci_indexed: false,
        webOfScienceDatabase: false,
        correspondingAuthor: false,
        guideOfAnyCoAuthor: false,
        googleScholarIndexed: false,
        isArticleInPubMedList: false,
        indexCoperificus: false,
        isArticleInUGCJournalList: false,
        articleFrontPage: "",
        articleWebLink: "",
       
    });

    const [disableSignUpButton, setDisableSignUpButton] = useState(true);

    // useEffects

    useEffect(() => {
        if (journalFormState['authors'].length > 0 && journalFormState['manuscriptTitle'] !== "" && journalFormState['fullJournalName'] !== "" && journalFormState["publisher"] !== "" && journalFormState["e_issn"] !== "" && journalFormState["p_issn"] !== "" && journalFormState["articleWebLink"] !== "" && journalFormState["doi"] != "") {
            setActiveStep((prevState) => { return { ...prevState, nextButton: false } })
        } else {
            setActiveStep((prevState) => { return { ...prevState, nextButton: true } });
        }
    }, [journalFormState, journalFormProof]);

    useEffect(() => {
        if (conferenceFormState['authors'].length > 0 && conferenceFormState['yearAndMonth']?.getMonth() && conferenceFormState['title'] !== "" && conferenceFormState['conferenceName'] !== "" && conferenceFormState['organizer'] !== "" && conferenceFormState['issueNo'] !== "" && conferenceFormProof !== null) {
            setActiveStep((prevState) => { return { ...prevState, nextButton: false } });
        } else {
            setActiveStep((prevState) => { return { ...prevState, nextButton: true } });
        }
    }, [conferenceFormState, conferenceFormProof]);

    // handle submit functions

    const setInitialState = async () => {
        setOpenBackdrop(false);
        setSnackbar({
            open: false,
            message: ""
        });
        setOpenDownloadDialog(false);
        setOpenJournalDialog(false);


        setActiveStep({
            stepperState: 0,
            nextButton: true
        });
        setOpenJournalForm({
            state: false,
            type: ''
        });

        setJournalFormState({
            authors: [],
            doi: "",
            manuscriptTitle: "",
            fullJournalName: "",
            publisher: "",
            publicationType: "",
            publicationStatus: "",
            yearAndMonth: null,
            e_issn: "",
            p_issn: "",
            volumeNo: "",
            issueNo: "",
            isMMDUAffilation: false,
            scopusIndexed: false,
            belongsToMMDUStudentsWork: false,
            areYouFirstAuthor: false,
            sci_scie_ssci_indexed: false,
            webOfScienceDatabase: false,
            correspondingAuthor: false,
            guideOfAnyCoAuthor: false,
            googleScholarIndexed: false,
            isArticleInPubMedList: false,
            indexCoperificus: false,
            isArticleInUGCJournalList: false,
            articleFrontPage: "",
            articleWebLink: "",
        });

        setConferenceFormState({
            authors: [],
            doi: "",
            title: "",
            conferenceName: "",
            organizer: "",
            conferenceType: "",
            paperStatus: "",
            yearAndMonth: null,
            volumeNo: "",
            issueNo: "",
            isbnNo: "",
            isMMDUAffilation: false,
            scopusIndexed: false,
            belongsToMMDUStudentsWork: false,
            areYouFirstAuthor: false,
            sci_scie_ssci_indexed: false,
            webOfScienceDatabase: false,
            correspondingAuthor: false,
            guideOfAnyCoAuthor: false,
            googleScholarIndexed: false,
            isArticleInPubMedList: false,
            indexCoperificus: false,
            isArticleInUGCJournalList: false,
            articleFrontPage: "",
            articleWebLink: "",
        });

    };

    const handleAddJournalSubmit = async () => {
        console.log(journalFormState);
        const journalApiData = new FormData();

        journalApiData.append('authors', JSON.stringify(journalFormState['authorName']));
        journalApiData.append('manuscriptTitle', journalFormState['manuscriptTitle']);
        journalApiData.append('journalName', journalFormState['journalName']);
        journalApiData.append('publisherName', journalFormState['publisherName']);
        journalApiData.append('publicationType', journalFormState['publicationType']);
        journalApiData.append('publicationStatus', journalFormState['publicationStatus']);
        journalApiData.append('yearAndMonth', journalFormState['yearAndMonth']);
        journalApiData.append('eissn', journalFormState['eissn']);
        journalApiData.append('pissn', journalFormState['pissn']);
        journalApiData.append('doi', journalFormState['doi']);
        journalApiData.append('articleWebLink', journalFormState['articleWebLink']);
        journalApiData.append('mmduAffliation', journalFormState['mmduAffliation']);
        journalApiData.append('mmduStudentWork', journalFormState['mmduStudentWork']);
        journalApiData.append('correspondingAuhor', journalFormState['correspondingAuthor']);
        journalApiData.append('guideOfCoAuthor', journalFormState['guideOfCoAuthor']);
        journalApiData.append('pbMedList', journalFormState['pbMedList']);
        journalApiData.append('indexedCorpernicus', journalFormState['indexedCorpernicus']);
        journalApiData.append('scopusIndexed', journalFormState['scopusIndexed']);
        journalApiData.append('firstAuthor', journalFormState['firstAuthor']);
        journalApiData.append('scisciessciIndexed', journalFormState['scisciessciIndexed']);
        journalApiData.append('webofScience', journalFormState['webofScience']);
        journalApiData.append('googleScholar', journalFormState['googleScholar']);
        journalApiData.append('ugcList', journalFormState['ugcList']);
        journalApiData.append('articleFrontPage', journalFormProof);
        journalApiData.append('volumeNo',journalFormState['volumeNo']);
        journalApiData.append('issueNo',journalFormState['issueNo']);
        const response = await addJournalConference('journal', journalApiData);
        
        alert(response['message']);
        // await setInitialState();
    };

    const handleAddConferenceSubmit = async () => {
        console.log(conferenceFormState);
        const conferenceApiData = new FormData();
        conferenceApiData.append('authors', JSON.stringify(conferenceFormState['authors']));
        conferenceApiData.append('title', conferenceFormState['title']);
        conferenceApiData.append('conferenceName', conferenceFormState['conferenceName']);
        conferenceApiData.append('organizer', conferenceFormState['organizer']);
        conferenceApiData.append('conferenceType', conferenceFormState['conferenceType']);
        conferenceApiData.append('paperStatus', conferenceFormState['paperStatus']);
        conferenceApiData.append('yearAndMonth', conferenceFormState['yearAndMonth']);
        conferenceApiData.append('volumeNo', conferenceFormState['volumeNo']);
        conferenceApiData.append('issueNo', conferenceFormState['issueNo']);
        conferenceApiData.append('isbnNo', conferenceFormProof['isbnNo']);
        conferenceApiData.append('doi', conferenceFormState['doi']);
        conferenceApiData.append('articleWebLink', conferenceFormState['articleWebLink']);
        conferenceApiData.append('isMMDUAffilation', conferenceFormState['belongsToMMDUStudentsWork']);
        conferenceApiData.append('belongsToMMDUStudentsWork', conferenceFormState['belongsToMMDUStudentsWork']);
        conferenceApiData.append('correspondingAuthor', conferenceFormState['correspondingAuthor']);
        conferenceApiData.append('guideOfAnyCoAuthor', conferenceFormState['guideOfAnyCoAuthor']);
        conferenceApiData.append('isArticleInPubMedList', conferenceFormState['isArticleInPubMedList']);
        conferenceApiData.append('indexCoperificus', conferenceFormState['indexCoperificus']);
        conferenceApiData.append('scopusIndexed', conferenceFormState['scopusIndexed']);
        conferenceApiData.append('areYouFirstAuthor', conferenceFormState['areYouFirstAuthor']);
        conferenceApiData.append('scisciessciIndexed', conferenceFormState['scisciessciIndexed']);
        conferenceApiData.append('webOfScienceDatabase', conferenceFormState['webOfScienceDatabase']);
        conferenceApiData.append('googleScholarIndexed', conferenceFormState['googleScholarIndexed']);
        conferenceApiData.append('isArticleInUGCJournalList', conferenceFormState['isArticleInUGCJournalList']);
        conferenceApiData.append('articleFrontPage', conferenceFormProof);
        const response = await addJournalConference('conference', conferenceApiData);
        alert(response['message']);
        await setInitialState();
    };



    const fetchPapers = async () => {
        // fetching papers
        const response = await getAllPapers();

        // storing papers
        await store.dispatch({
            type: 'storeJournalPapers',
            payload: Object.values(response.data.journalPapers)
        });

        await store.dispatch({
            type: 'storeConferencePapers',
            payload: Object.values(response.data.conferencePapers)
        });

        await store.dispatch({
            type: 'storePatentPapers',
            payload: Object.values(response.data.patentPapers)
        });

        await store.dispatch({
            type: 'storeCopyrightPapers',
            payload: Object.values(response.data.copyrightPapers)
        });
    };

    useEffect(async () => {
        await fetchPapers();
    }, [])

    

    const fetchAllUsers = async () => {
        const response = await fetchUsers();
        if (response.status === 200) {
            store.dispatch({
                type: 'addUserList',
                payload: response.data
            });
        } else {
            alert(`Response Status ${response.status}\n.Response Data : ${response.data}`);
        }
    };

    const fetchUnapprovedAccounts = async() => {
        const response = await getUnapprovedAccounts();

        if(response.status === 200){
            setUnapprovedAccounts(response.data);
        } else {
            alert(`Response Status ${response.status}\nMessage : ${response.data}`);
        }
    };


    const handleApproveAction = async(_id, type) => {
        let data = {
            _id : _id
        };
        let response;
        if(type === "Approve"){
            response = await approveAccount(data);
        } else {
            response = await rejectAccount(data);
        }

        alert(`Response Status ${response.status}\n Message : ${response.data}`);

        await fetchUnapprovedAccounts();

    }

    // final state of the app

    const finalState = {
        backdrop: [openBackdrop, setOpenBackdrop],
        downloadDocxDialog: [openDownloadDocxDialog, setOpenDownloadDialog],
        journalDialog: [openJournalDialog, setOpenJournalDialog],
        conferenceDialog: [openConferenceDialog, setOpenConfereceDialog],
        conferenceForm: [openConferenceForm, setOpenConferenceForm],
        journalForm: [openJournalForm, setOpenJournalForm],
        conferenceFormData: [conferenceFormState, setConferenceFormState],
        conferenceFormFile: [conferenceFormProof, setConferenceFormProof],
        journalFormData: [journalFormState, setJournalFormState],
        journalFormFile: [journalFormProof, setJournalFormProof],
        stepperActiveStep: [activeStep, setActiveStep],
        handleAddJournalSubmit: handleAddJournalSubmit,
        handleAddConferenceSubmit: handleAddConferenceSubmit,
        snackbar: [snackbar, setSnackbar],
        fetchPapers: fetchPapers,
        fetchUsersList: fetchAllUsers,
        tabStatus: tabStatus,
        setTabStatus: setTabStatus, 
        fetchUnapprovedAccounts : fetchUnapprovedAccounts, 
        unapprovedAccounts : unapprovedAccounts, 
        handleApproveAction : handleApproveAction
    };


    return (
        <GlobalStateContext.Provider value={finalState}>
            {children}
        </GlobalStateContext.Provider>
    );
}

export { GlobalStateContext, GlobalStateProvider };