const initialState = {
    journalPapers : [], 
    conferencePapers : [], 
    patentPapers : [], 
    copyrightPapers : []
};

export default function papersReducer(state = initialState, action) {
    switch (action.type){
        case 'storeJournalPapers' : {
            return {
                ...state, 
                journalPapers : action.payload
            };
        }
        case 'storeConferencePapers' : {
            return {
                ...state, 
                conferencePapers : action.payload
            };
        }
        case 'storePatentPapers' : {
            return {
                ...state, 
                patentPapers : action.payload
            };
        }
        case 'storeCopyrightPapers' : {
            return {
                ...state, 
                copyrightPapers : action.payload
            };
        } 
        default : return state
    }
}