import React from 'react';
import ListItem from '@mui/material/ListItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import MenuItem from '@mui/material/MenuItem';
import Checkbox from '@mui/material/Checkbox';
import Button from '@mui/material/Button';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormGroup from '@mui/material/FormGroup';
import Select from '@mui/material/Select';
import InputLabel from '@mui/material/InputLabel';
import FormControl from '@mui/material/FormControl';
import List from '@mui/material/List';


export default function SelectComponent({state, handleChange}){

    
    
    return(
        <List>
            <ListItem>
        <ListItemIcon>
            <strong>Report Type</strong>
            <ListItemText/>
        </ListItemIcon>
    </ListItem>
    <ListItem>
        <ListItemIcon>
            <FormControl  sx={{ m: 0, width: 250 }}>
            <InputLabel id="demo-simple-select-label">Unique Publications</InputLabel>
            <Select
              labelId="demo-simple-select-label"
              id="publicationType"
              name="type"
              value={state['type']}
              label="Unique Publications"
              onChange={(e) => handleChange(e.target.name, e.target.value)}
            >
              <MenuItem value="">Select</MenuItem>
              <MenuItem value="National">National</MenuItem>
              <MenuItem value="International">International</MenuItem>
            </Select>
          </FormControl>
            <ListItemText/>
        </ListItemIcon>
    </ListItem>
    <ListItem>
        <ListItemIcon>
            <strong>DISPLAY</strong>
            <ListItemText/>
        </ListItemIcon>
    </ListItem>
    <ListItem>
        <ListItemIcon>
        <FormControl  sx={{ m: 0, width: 250 }}>
            <InputLabel id="demo-simple-select-label">Order</InputLabel>
            <Select
              labelId="demo-simple-select-label"
              id="order"
              name="order"
              value={state['order']}
              label="Order"
              onChange={(e) => handleChange(e.target.name, e.target.value)}
            >
              <MenuItem value="Ascending">Ascending</MenuItem>
              <MenuItem value="Descending">Descending</MenuItem>
            </Select>
          </FormControl>
            <ListItemText/>
        </ListItemIcon>
    </ListItem>
    <ListItem>
          <ListItemIcon>
            <FormGroup row>
            <FormControlLabel control={
                <Checkbox
                checked={state['showDetail']}
                onChange={(e) => handleChange(e.target.name, e.target.checked)}
                name="showDetail"
                id="showDetail"
                color="primary"
                />     
            }
            label="Show Detail"
            />
            </FormGroup>
          </ListItemIcon>
          <ListItemText/>
      </ListItem>
      <ListItem>
        <ListItemIcon>
            <Button variant="contained" color="primary" disabled>Update Scopus Citation</Button>
            <ListItemText/>
        </ListItemIcon>
    </ListItem>

        </List>
    );
}