import React from 'react';
import {GlobalPCStateProvider} from '../features/States/PatentStates';
import {GlobalStateProvider} from '../features/mainState';
import { GlobalJCStateProvider } from './States/JournalStates';
import { GlobalJCDrawerProvider } from './States/JCDrawerState';
import FilterProvider from './States/DataTableFilterState';

export default function UpperState({children}){

    return (
        <GlobalStateProvider>
            <GlobalPCStateProvider>
                <GlobalJCStateProvider>
                    <GlobalJCDrawerProvider>
                        <FilterProvider.GlobalJCFilterProvider>
                            {children}
                        </FilterProvider.GlobalJCFilterProvider>
                    </GlobalJCDrawerProvider>
                </GlobalJCStateProvider>
            </GlobalPCStateProvider>
        </GlobalStateProvider>
    );
}