import React, {useContext} from 'react';
import Avatar from '@mui/material/Avatar';
import List from '@mui/material/List';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import DialogTitle from '@mui/material/DialogTitle';
import Dialog from '@mui/material/Dialog';
import { GlobalPCStateContext } from '../../features/States/PatentStates';
import { DialogContent, Typography } from '@mui/material';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import Divider from '@mui/material/Divider';
import { PatentIcon,CopyrightIcon } from '../Icons/icon';
export default function PatentDialog() {

  const {openDialog, setOpenDialog, setOpenForm} = useContext(GlobalPCStateContext);

  const handleClose1 = () => {
    setOpenDialog(false);
  }

  const setType = (type) => {
    setOpenForm({
      state : true, 
      type : type
    });
  }

  

  const buttonContent1 = [
    {text : 'Patent', onClick :() => {setType('Patent');}, key : 'Patent', icon :<PatentIcon/>},
    {text : 'Copyright', onClick : () => {setType('Copyright');}, key : 'Copyright', icon :<CopyrightIcon/>},
];

  return (
    <Dialog onClose={handleClose1} open={openDialog}>
      <DialogTitle >
        <Typography variant="h5" gutterBottom >Add Patent / Copyright</Typography>
      </DialogTitle>
      <DialogContent>
            <List>
                {buttonContent1.map((item) => (
                    <ListItem button onClick={item.onClick} key={item.key}>
                        <ListItemAvatar>
                            <Avatar>
                                {item.icon}
                            </Avatar>
                        </ListItemAvatar>
                        <ListItemText>
                          <Typography variant="h8">{item.text}</Typography>
                        </ListItemText>
                        <Divider/>
                    </ListItem>
                ))}
            </List>
      </DialogContent>
    </Dialog>
  );
}