import React, {useState, useEffect, createContext} from 'react';
import { connect } from 'react-redux';

const GlobalJCDrawerContext = createContext();

function GlobalJCDrawerProvider(props){

    const [state, setState] = useState({
        status : "",
        type : '',
        sciIndexed: false,
        scopusIndexed: false,
        googleScholar: false,
        ugcListed: false,
        pubmedIndexed: false,
        mmuAffiliation: false,
        mmuStudentWork: false,
        afterJoiningMMU: false,
        showDetail: false,
        order : 'Ascending',
        to : new Date(), 
        from : new Date(),
        dateFilter : false,
        paperToApprove:false
    });

    // const [attributeSet, setAttributeSet] = useState(new Set());



    const [journalRow, setJournalRow] = useState(props.journal);
    const [conferenceRow, setConferenceRow] = useState(props.conference);
    const [patentRow, setPatentRow] = useState(props.patent);
    const [copyrightRow, setCopyrightRow] = useState(props.copyright);





    const handleChange = (name, value) => {
        // console.log(name, value);
        setState({
            ...state, 
            [`${name}`] : value
        });
    };

    useEffect(() => {
        if(state['to'] !== state['from']){
            // console.log("pavan")
            handleChange('dateFilter', true);
            // console.log("dateFilter", state['dateFilter']);
        } else {
            handleChange('dateFilter', false);
        }
    }, [state['to'], state['from']]);

    const finalObject = {
        state : state,
        handleChange : handleChange,
        journal : journalRow,
        conference : conferenceRow,
        patent : patentRow, 
        copyright : copyrightRow, 
    };

    return (
        <GlobalJCDrawerContext.Provider value={finalObject}>
            {props.children}
        </GlobalJCDrawerContext.Provider>);

}

export {GlobalJCDrawerContext, GlobalJCDrawerProvider};

function mapStateToProps(state) {
    const { papers } = state
    return { journal : papers['journalPapers'], conference : papers['conferencePapers'], patent : papers['patentPapers'], copyright : papers['copyrightPapers']};
  }
  
  export default {
    GlobalJCDrawerProvider : connect(mapStateToProps)(GlobalJCDrawerProvider),
    GlobalJCDrawerContext : GlobalJCDrawerContext
  }