import React, { useState } from 'react';
import { Stack, List, ListItem, ListItemIcon, FormControl, InputLabel, Select, MenuItem, FormControlLabel, Checkbox } from '@mui/material';
import DatePickerComponent from '../../components/DatePicker/datepicker';

export default function DrawerPatentCopyright() {

    const [status, setStatus] = useState({
        applied: false,
        registered: false,
        granted: false,
        published: false
    });

    const handleChange = (event) => {
        setStatus({
            ...status,
            [event.target.name]: event.target.checked,
        });
    };


    return (
        <Stack direction="column" spacing={2} sx={{ bgcolor: 'white', height: '100%', width: '100%' }}>
            <Stack direction="column" spacing={2} sx={{ bgcolor: 'white', width: '100%' }}>
                <DatePickerComponent />
            </Stack>
            <Stack direction="column" spacing={1} sx={{ bgcolor: 'white', width: '100%', p: 2 }}>
                <ListItemIcon><strong>STATUS</strong></ListItemIcon>
                <FormControl>
                    <FormControlLabel
                        control={
                            <Checkbox checked={status['applied']} onChange={handleChange} name="applied" />
                        }
                        label="Applied"
                    />
                    <FormControlLabel
                        control={
                            <Checkbox checked={status['registered']} onChange={handleChange} name="registered" />
                        }
                        label="Registered"
                    />
                    <FormControlLabel
                        control={
                            <Checkbox checked={status['published']} onChange={handleChange} name="published" />
                        }
                        label="Published"
                    />
                    <FormControlLabel
                        control={
                            <Checkbox checked={status['granted']} onChange={handleChange} name="granted" />
                        }
                        label="Granted"
                    />
                </FormControl>
            </Stack>
        </Stack>
    );
}