import React, {useContext} from 'react';
import Avatar from '@mui/material/Avatar';
import List from '@mui/material/List';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import DialogTitle from '@mui/material/DialogTitle';
import Dialog from '@mui/material/Dialog';
import { GlobalStateContext } from '../../features/mainState';
import { DialogContent } from '@mui/material';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import Divider from '@mui/material/Divider';
import { GlobalJCStateContext } from '../../features/States/JournalStates';
import { JournalIcon,ConferenceIcon } from '../Icons/icon';

export default function JournalDialog() {
  const {journalDialog, journalForm,conferenceDialog,conferenceForm} = useContext(GlobalStateContext);

  const {openDialogJC, setOpenDialogJC, setOpenForm} = useContext(GlobalJCStateContext);

  const setType = (e) => {
    setOpenForm({
      state : true, 
      type : e
    });
  }

  const handleClose = () => {
    setOpenDialogJC(false);
  }

  

  const buttonContent = [
      {text : 'Journal', onClick : () => setType('Journal'), key : 'Journal', icon :<JournalIcon/>},
      {text : 'Conference', onClick : () => setType('Conference'), key : 'Conference', icon :<ConferenceIcon/> },
  ];

  return (
    <Dialog onClose={handleClose} open={openDialogJC}>
      <DialogTitle>Add Journal/Conference</DialogTitle>
      <DialogContent >
            <List>
                {buttonContent.map((item) => (
                    <ListItem button onClick={item.onClick} key={item.key} name={item.key}>
                        <ListItemAvatar>
                            <Avatar>
                                {item.icon}
                            </Avatar>
                        </ListItemAvatar>
                        <ListItemText>{item.text}</ListItemText>
                        <Divider/>
                    </ListItem>
                ))}
            </List>
      </DialogContent>
    </Dialog>
  );
}