import React, {useState, createContext, useEffect} from 'react';
import { useDispatch } from 'react-redux';
import { Login, createAccountAPI, logout } from '../../api/main';
import { useHistory } from "react-router-dom";
import { store } from '../../redux/store';


const UserStateContext = createContext();

function UserStateProvider({children}) {

    const dispatch = useDispatch();
    const history = useHistory();

    const [loginState, setLoginState] = useState({
        role : "", 
        employeeId : "", 
        password : ""
    });

    const [disableLoginButton, setDisableLoginButton] = useState(true);
    const [myRole,setMyRole]=useState('');
    const [isLogged,setIsLogged]=useState(false);

    const [createAccount, setCreateAccount] = useState({
        name : "", 
        employeeId : "", 
        email : "",
        role : "", 
        password : "", 
        confirmPassword : ""
    });

    const [disableSignUpButton, setDisableSignUpButton] = useState(true);

    const [profilePic, setProfilePic] = useState(null);


    useEffect(() => {
        if (loginState['role'] !== '' && loginState['employeeId'] !== '' && loginState['password'] !== '') {
            setDisableLoginButton(false);
        } else {
            setDisableLoginButton(true);
        }
    }, [loginState]);

    useEffect(() => {
        if (createAccount['name'] !== "" && createAccount['id'] !== "" && createAccount['email'] !== "" && createAccount['role'] !== "Select" && createAccount['confirmPassword'] !== "") {
            setDisableSignUpButton(false);
        } else {
            setDisableSignUpButton(true);
        }
    }, [createAccount]);

    const initializeLogin = () => {
        setLoginState({
            role: '',
            employeeId: '',
            password: ''
        });
    }

    const initializeCreateAccount = () => {
        setCreateAccount({
            name: "",
            employeeId: "",
            email: "",
            role: "Select",
            password: "",
            confirmPassword: "",
        });
    }

    const handleCreateAccount = async () => {
        let createAccountData = new FormData();
        createAccountData.append('name', createAccount['name']);
        createAccountData.append('role', createAccount['role']);
        createAccountData.append('employeeId', createAccount['employeeId']);
        createAccountData.append('email', createAccount['email']);
        createAccountData.append('password', createAccount['password']);
        createAccountData.append('profilePic', profilePic);

        const response = await createAccountAPI(createAccountData);

        alert(`Message : ${response.message}`);
        if (response.status === 200) {
            history.push('/');
        }

        initializeCreateAccount();
    }

    const handleLogin = async () => {

        const response = await Login(loginState);

        if (response.status === 200) {
            var user = {
                name: response.data.name,
                employeeId: response.data.employeeId,
                email: response.data.email,
                id: response.data.id,
                role: response.data.role
            };

            setProfilePic(response.profilePic);

            setMyRole(response.data.role);

            // localStorage.setItem('profilePic', response.profilePic);

            dispatch({
                type: 'user/addUser',
                payload: user
            });

            history.push('/homePage');
            setIsLogged(true);

        } else {
            alert(`Message: ${response.data.message}`);
        }

        initializeLogin();
    };

    const handleLogout = async () => {
        await logout();
        await store.dispatch({
            type: 'user/removeUser',
            payload: {}
        });
        await store.dispatch({
            type: 'storeJournalPapers',
            payload: []
        });
        await store.dispatch({
            type: 'storeConferencePapers',
            payload: []
        });
        await store.dispatch({
            type: 'storePatentPapers',
            payload: []
        });
        await store.dispatch({
            type: 'storeCopyrightPapers',
            payload: []
        });
        await store.dispatch({
            type: 'addUserList',
            payload: []
        })
        history.push('/');
    };

    const value = {
        loginState : [loginState, setLoginState], 
        loginButton: disableLoginButton,
        handleLogin: handleLogin,
        createAccountObject: [createAccount, setCreateAccount],
        signupButton: disableSignUpButton,
        handleCreateAccount: handleCreateAccount,
        handleLogout: handleLogout,
        profilePic: [profilePic, setProfilePic], 
        myRole : myRole,
        isLogged:isLogged
    };

    return (
        <UserStateContext.Provider value={value}>
            {children}
        </UserStateContext.Provider>
    );
}

export {UserStateContext, UserStateProvider};