import React, {useContext} from 'react';
import Avatar from '@mui/material/Avatar';
import List from '@mui/material/List';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import DialogTitle from '@mui/material/DialogTitle';
import Dialog from '@mui/material/Dialog';
import { GlobalStateContext } from '../../features/mainState';
import { DialogContent } from '@mui/material';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import Divider from '@mui/material/Divider';
import FileDownloadIcon from '@mui/icons-material/FileDownload';
import {downloadDocx} from '../../api/main';

const emails = ['username@gmail.com', 'user02@gmail.com'];

export default function DowloadDocx() {
  const {downloadDocxDialog} = useContext(GlobalStateContext);

  const handleClose = () => {
    downloadDocxDialog[1](false);
  };

  const handleDownloadDocx = async (type) => {
    await downloadDocx(type);
  };

  const buttonContent = [
      {text : 'Journal Report', onClick : () => {handleDownloadDocx('Journal');}, key : 'Journal'},
      {text : 'Conference Report', onClick : () => {handleDownloadDocx('Conference');}, key : 'Conference'},
      {text : 'Patent Report', onClick : () => {handleDownloadDocx('Patent');}, key : 'Patent'},
      {text : 'Copyright Report', onClick : () => {handleDownloadDocx('Copyright');}, key : 'Copyright'}
  ];

  return (
    <Dialog onClose={handleClose} open={downloadDocxDialog[0]}>
      <DialogTitle>Download Reports</DialogTitle>
      <DialogContent>
            <List>
                {buttonContent.map((item) => (
                    <ListItem button onClick={item.onClick} key={item.key}>
                        <ListItemAvatar>
                            <Avatar>
                                <FileDownloadIcon/>
                            </Avatar>
                        </ListItemAvatar>
                        <ListItemText>{item.text}</ListItemText>
                        <Divider/>
                    </ListItem>
                ))}
            </List>
      </DialogContent>
    </Dialog>
  );
}