import React, { useContext, useEffect } from 'react';
import { styled } from '@mui/system';
import TabsUnstyled from '@mui/base/TabsUnstyled';
import TabsListUnstyled from '@mui/base/TabsListUnstyled';
import TabPanelUnstyled from '@mui/base/TabPanelUnstyled';
import { buttonUnstyledClasses } from '@mui/base/ButtonUnstyled';
import TabUnstyled from '@mui/base/TabUnstyled';
import {Box, IconButton} from '@mui/material';
import { DataGrid } from '@mui/x-data-grid';
import {AdminStateContext} from '../../features/States/Adminstate';
import Stack from '@mui/material/Stack';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import CancelIcon from '@mui/icons-material/Cancel';



const blue = {
  500: '#0099FF',
};

const Tab = styled(TabUnstyled)`
  font-family: IBM Plex Sans, sans-serif;
  color: white;
  cursor: pointer;
  font-size: 0.875rem;
  font-weight: bold;
  background-color: transparent;
  width: 100%;
  padding: 12px 16px;
  margin: 6px 6px;
  border: none;
  border-radius: 5px;
  display: flex;
  justify-content: center;
`;

const TabPanel = styled(TabPanelUnstyled)`
  width: 100%;
  font-family: IBM Plex Sans, sans-serif;
  font-size: 0.875rem;
`;

const TabsList = styled(TabsListUnstyled)`
  min-width: 320px;
  background-color: ${blue[500]};
  border-radius: 8px;
  margin-bottom: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  align-content: space-between;
`;

function RenderCell(cellValues) {
  return (
    <div
    style={{
      fontSize: 13,
      width: "100%",
      textAlign: "center"
    }}
    >{cellValues.value}</div>
  );
}

function ActionButton(params) {

  const {handleApproveAction} = useContext(AdminStateContext);

  const onClick = async (type) => {
    await handleApproveAction(params.row._id, type);
  };

  return (
    <Stack direction="row" spacing={1} sx={{width : '100%', justifyContent : 'center'}}>
        <IconButton name="Approve" onClick={() => onClick("Approve")}>
          <CheckCircleIcon sx={{color : 'green'}}/>
        </IconButton>
        <IconButton name="Reject" onClick={() => onClick("Reject")}>
          <CancelIcon sx={{color : 'red'}}/>
        </IconButton>
    </Stack>
  );
}

export default function ApprovalList() {

  const header = [
    { field: "id", headerName: "Sno.", headerClassName: 'header', width: 115, sortable: false, disableColumnMenu: true, disableColumnFilter: true, disableColumnSelector: true, headerAlign: 'center', renderCell : RenderCell},

    { field: "name", headerName: "Name", headerClassName: 'header', width: 210, sortable: false, disableColumnMenu: true, disableColumnFilter: true, disableColumnSelector: true, headerAlign: 'center', renderCell : RenderCell},

    { field: "employeeId", headerName: "Employee Id", headerClassName: 'header', width: 210, sortable: false, disableColumnMenu: true, disableColumnFilter: true, disableColumnSelector: true, headerAlign: 'center', renderCell : RenderCell},

    { field: "email", headerName: "Email Id", headerClassName: 'header', width: 210, sortable: false, disableColumnMenu: true, disableColumnFilter: true, disableColumnSelector: true, headerAlign: 'center', renderCell : RenderCell},

    { field: "role", headerName: "Role", headerClassName: 'header', width: 210, sortable: false, disableColumnMenu: true, disableColumnFilter: true, disableColumnSelector: true, headerAlign: 'center', renderCell : RenderCell},
    
    // { field: "date", headerName: "Requested Date", headerClassName: 'header', width: 200, sortable: false, disableColumnMenu: true, disableColumnFilter: true, disableColumnSelector: true, headerAlign: 'center', },

    { field: "action", headerName: "Actions", headerClassName: 'header', width: 210, sortable: false, disableColumnMenu: true, disableColumnFilter: true, disableColumnSelector: true, headerAlign: 'center', renderCell : ActionButton}
  ]

  const {fetchUnapprovedAccounts, unapprovedAccounts} = useContext(AdminStateContext);

  useEffect( async () => {
    await fetchUnapprovedAccounts();
  }, []);


  return (

    <Box sx={{
      height: 600, width: '82%', m : 'auto', backgroundColor: 'white',
      '& .header': {
        fontFamily: "Bebas Neue",
        fontSize: '25px'
      }, 
    }} variant="outlined">
      <div style={{ height: 550, width: '100%', backgroundColor : 'white', marginTop : 30}}>
            <DataGrid
              sx={{
                  "& .MuiDataGrid-columnHeaders": {
                    fontFamily: 'IBM Plex Sans, sans-serif',
                    fontSize: '5%',
                    backgroundColor: 'black',
                    color: "white",
                    fontWeight : '100px'
                  },
                  '&.MuiDataGrid-root .MuiDataGrid-cell:focus, &.MuiDataGrid-root .MuiDataGrid-columnHeader:focus': {
                    outline: 'none',
                  },
                  textAlign: 'center',
                  boxShadow: 2,
                  border: 1,
                  borderColor: 'black',
                  '& .MuiDataGrid-cell:hover': {
                    color: 'primary.main',
                  },
              }}
              rows={unapprovedAccounts}
              columns={header}
              pageSize={10}
              rowsPerPageOptions={[10]}
            />
          </div>
    </Box>
  );
}
