import React, {useState, useContext} from 'react';
import Box from '@mui/material/Box';
import SpeedDial from '@mui/material/SpeedDial';
import SpeedDialIcon from '@mui/material/SpeedDialIcon';
import SpeedDialAction from '@mui/material/SpeedDialAction';
import {GlobalStateContext} from '../../features/mainState';
import { GlobalPCStateContext } from '../../features/States/PatentStates';
import { GlobalJCStateContext } from '../../features/States/JournalStates';
import DownloadIcon from '@mui/icons-material/Download';
import FileCopyRoundedIcon from '@mui/icons-material/FileCopyRounded';


export default function ControlledOpenSpeedDial() {
    const {backdrop, downloadDocxDialog} = useContext(GlobalStateContext);
    const {setOpenDialog} = useContext(GlobalPCStateContext);
    const {setOpenDialogJC} = useContext(GlobalJCStateContext);
  const [open, setOpen] = useState(false);

  const handleOpen = () => {
    setOpen(true);
    backdrop[1](true);
  };
  const handleClose = () => {
    setOpen(false);
    backdrop[1](false);
  }

  const actions = [
    //   { icon: <FileCopyIcon />, name: 'Copy' },
      { icon: <FileCopyRoundedIcon  />, name: 'Add Publications' ,onClick : () => setOpenDialogJC(true)},
      { icon: <FileCopyRoundedIcon />, name: 'Add Patent/Copyright' ,onClick : () => setOpenDialog(true)},
      { icon: <DownloadIcon />, name: 'Download Report', onClick : () => {downloadDocxDialog[1](true)} },
    ];
  


  return (
    <Box sx={{ height: 80,width : '100%',flexDirection: 'row-reverse', transform: 'translateZ(0px)', flexGrow: 1 ,}}>
      <SpeedDial
        ariaLabel="SpeedDial controlled open example"
        sx={{ position: 'absolute', bottom: 16, right: 16 }}
        icon={<SpeedDialIcon/>}
        onClose={handleClose}
        onOpen={handleOpen}
        open={open}
        FabProps={{
          sx: {
            bgcolor: 'black',
            '&:hover': {
              bgcolor: 'black',
            }
          }
        }}
      >
        {actions.map((action) => (
          <SpeedDialAction
            key={action.name}
            icon={action.icon}
            tooltipTitle={action.name}
            onClick={action.onClick}
          />
        ))}
      </SpeedDial>
    </Box>
  );
}
