import React, {useState, useEffect, createContext, useContext} from 'react';
import { GlobalJCDrawerContext } from './JCDrawerState';
import { connect } from 'react-redux';


const GlobalJCFilterContext = createContext();

function GlobalJCFilterProvider(props){

    const {state} = useContext(GlobalJCDrawerContext);

    const initFunc = () => {
        setJournalRow(props.journal);
        setConferenceRow(props.conference);   
    };

    const [journalRow, setJournalRow] = useState([]);
    const [conferenceRow, setConferenceRow] = useState([]);
    const [patentRow, setPatentRow] = useState([]);
    const [copyrightRow, setCopyrightRow] = useState([]);

    const [attributeSet, setAttributeSet] = useState([]);
    // let journalRow = [], conferenceRow = [], patentRow = [], copyrightRow = []; 

    // for journal/conference

    useEffect(async () => {
        setJournalRow(props.journal);
        setConferenceRow(props.conference);
        setPatentRow(props.patent);
        setCopyrightRow(props.copyright);
    }, [props.journal, props.conference, props.patent, props.copyright]);



    useEffect(async() => {
        let flag = true;
        
        if(attributeSet.length > 0){
            if(state['status'] !== ""){
                setJournalRow(journalRow.filter(item => item.publicationStatus === state['status']));
                setConferenceRow(conferenceRow.filter(item => item.paperStatus === state['status']));
            }
            flag = false;
        } else {
            if(state['status'] !== ""){
                setJournalRow(props.journal.filter(item => item.publicationStatus === state['status']));
                setConferenceRow(props.conference.filter(item => item.paperStatus === state['status']));
                flag = false;
            }
        }

        if(flag === true){
            setJournalRow(props.journal);
            setConferenceRow(props.conference);
        }

    }, [state['status']]);
    
    

    useEffect(async() => {
        
        if(state['scopusIndexed']){
            setAttributeSet([...attributeSet,'scopusIndexed']);
        } else {
            setAttributeSet(attributeSet.filter((val) => (val !== 'scopusIndexed')));
        }

    }, [state['scopusIndexed']]);


    
    useEffect(async() => {
        if(state['googleScholar']){
            setAttributeSet([...attributeSet,'googleScholarIndexed']);
        } else {
            setAttributeSet(attributeSet.filter((val) => (val !== 'googleScholarIndexed')));
        }
    }, [state['googleScholar']]);

    useEffect(async() => {
        if(state['sciIndexed']){
            setAttributeSet([...attributeSet,'sci_scie_ssci_indexed']);
        } else {
            setAttributeSet(attributeSet.filter((val) => (val !== 'sci_scie_ssci_indexed')));
        }
    }, [state['sciIndexed']]);
    
    useEffect(async() => {
        if(state['ugcListed']){
            setAttributeSet([...attributeSet,'isArticleInUGCJournalList']);
        } else {
            setAttributeSet(attributeSet.filter((val) => (val !== 'isArticleInUGCJournalList')));
        }
    }, [state['ugcListed']]);

    useEffect(async() => {
        if(state['pubmedIndexed']){
            setAttributeSet([...attributeSet,'isArticleInPubMedList']);
        } else {
            setAttributeSet(attributeSet.filter((val) => (val !== 'isArticleInPubMedList')));
        }
    }, [state['pubmedIndexed']]);
    
    useEffect(async() => {
        if(state['mmuAffiliation']){
            setAttributeSet([...attributeSet,'isMMDUAffilation']);
        } else {
            setAttributeSet(attributeSet.filter((val) => (val !== 'isMMDUAffilation')));
        }
    }, [state['mmuAffiliation']]);

    useEffect(async() => {
        if(state['mmuStudentWork']){
            setAttributeSet([...attributeSet,'belongsToMMDUStudentsWork']);
        } else {
            setAttributeSet(attributeSet.filter((val) => (val !== 'belongsToMMDUStudentsWork')));
        }
    }, [state['mmuStudentWork']]);

    // useEffect(async () => {
    //     if(state['dateFilter']){
    //         // console.log("Pavan");
    //         console.log("dateFilter", state['dateFilter']);
    //         let temp = journalRow;
    //         console.log(temp);
    //         temp.slice().sort(function(a, b){
    //             let d1 = a.monthAndYear;
    //             let d2 = b.monthAndYear;
    //             return (new Date(d1) - new Date(d2));   
    //         });
    //         // console.log("pavan",temp);
    //         setJournalRow(temp);
    //     }
    // }, [state['dateFilter']]);
    
    // useEffect(async() => {
    //     setAttributeSet(attributeSet.filter((val) => (val !== state['status'])));
    //     if(state['status'] !== ""){

    //         setAttributeSet([...attributeSet,state['status']]);
    //     } else {
    //         setAttributeSet(attributeSet.filter((val) => ((val !== "Approved") && (val !== "Pending") && (val !== "Disapproved"))));
    //     }
    // }, [state['status']]);

    useEffect(() => {
            setJournalRow(props.journal.filter(item => {
                let flag = true;
                let n = attributeSet.length;
                for(let i = 0; i< n;i++){
                    let val = attributeSet[i];
                    if((val === "Approved") || (val == "Pending") || (val === "Disapproved")){
                        if(item['publicationStatus'] !== val){
                            flag = false;
                            break;
                        }
                    }
                    if(item[val] === "false"){
                        flag = false;
                        break;
                    }
                }
                // console.log("pavan", flag);
                return flag;
            }));
            setConferenceRow(props.conference.filter(item => {
                let flag = true;
                let n = attributeSet.length;
                for(let i = 0; i< n;i++){
                    let val = attributeSet[i];
                    if((val === "Approved") || (val == "Pending") || (val === "Disapproved")){
                        if(item['paperStatus'] !== val){
                            flag = false;
                            break;
                        }
                    }
                    if(item[val] === "false"){
                        flag = false;
                        break;
                    }
                }
                // console.log("pavan", flag);
                return flag;
            }));
        // console.log(attributeSet);
    }, [attributeSet]);

    useEffect(() => {

    }, [])



    const finalObject = {
        journal : journalRow,
        conference : conferenceRow,
        patent : patentRow, 
        copyright : copyrightRow
    };

    return (
        <GlobalJCFilterContext.Provider value={finalObject}>
            {props.children}
        </GlobalJCFilterContext.Provider>);

}

function mapStateToProps(state) {
    const { papers } = state;
    return { journal : papers['journalPapers'], conference : papers['conferencePapers'], patent : papers['patentPapers'], copyright : papers['copyrightPapers']};
  }
  
  export default {
    GlobalJCFilterProvider : connect(mapStateToProps)(GlobalJCFilterProvider),
    GlobalJCFilterContext : GlobalJCFilterContext
};