import React, {useState,createContext, useEffect} from 'react';
import {getPatentPaper, getCopyrightPaper, addPatentCopyright, updatePatentCopyright} from '../../api/main';
import { store } from '../../redux/store'; 
import {useDispatch } from 'react-redux';
import { setYear } from 'date-fns';


const GlobalPCStateContext = createContext();

const months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];

const setAttribute = (atr, val, obj) => {
    let temp = obj;
    temp[atr] = val;
    return temp;
};

function GlobalPCStateProvider({children}){

    const dispatch = useDispatch();

    const [opr, setOpr] = useState('add');

    // patent or copyright dialog 
    const [openDialog, setOpenDialog] = useState(false);

    // patent or copyright form proof state
    const [formProof, setFormProof] = useState(null);

    // form stepper state
    const [activeStep, setActiveStep] = useState({
        state : 0, 
        disableButton : true
    });

    // open form state
    const [openForm, setOpenForm] = useState({
        state : false, 
        type : 'Patent'
    });

    // form field values
    const [formState, setFormState] = useState({
        nameOfInventors : [], 
        title : "", 
        applicationNo : "", 
        monthAndYear : null, 
        status : "",
        nameOfAgency : "", 
    });
    
    const [previewMode, setPreviewMode] = useState(false);

    const [nameOfInventors, setNameOfInventors] = useState([]);

    const [title, setTitle] = useState("");

    const [applicationNo, setApplicationNo] = useState("");

    const [monthAndYear, setMonthAndYear] = useState(null);

    const [status, setStatus] = useState("");

    const [nameOfAgency, setNameOfAgency] = useState("");

    const [_id, set_Id] = useState('');


    // use effect hook for validating form values
    useEffect(() => {
        if(nameOfInventors.length > 0 && monthAndYear !== null && title !== "" && nameOfAgency  !== "" && status !== "" && status !== "Select" && applicationNo  !== "" && formProof !== null){
            setActiveStep({...activeStep, disableButton : false});
        } else {
            setActiveStep({...activeStep, disableButton : true});
        }
    },[formState, formProof]);

    useEffect(() => {
        if(activeStep['state'] === 1){
            setPreviewMode(true);
        } else {
            setPreviewMode(false);
        }
    }, [activeStep]);

    useEffect(() => {
        if(openDialog === false){
            setOpenForm({
                state : false, 
                type : 'Patent'
            });
        }
    }, [openDialog]);

    useEffect(() => {
        if(openForm['state'] === false){
            setFormProof(null);
            setActiveStep({
                state : 0, 
                disableButton : true
            });
            setTitle("");
            setNameOfAgency("");
            setNameOfInventors([]);
            setPreviewMode(false);
            setMonthAndYear(null);
            setStatus("");
            setOpr('add');
        }
    }, [openForm['state']]);

    const handleGetPatentPaper = async() => {
        const response = await getPatentPaper();
        await store.dispatch({
            type : 'storePatentPapers', 
            payload : response
        });
    }

    const handleGetCopyrightPaper = async() => {
        const response = await getCopyrightPaper();
        await store.dispatch({
            type : 'storeCopyrightPapers', 
            payload : response
        });
    }

    // handle submit for form
    const handleSubmitForm = async () =>{
        const data = new FormData();

        data.append('nameOfInventors', JSON.stringify(nameOfInventors));
        data.append('title', title);
        data.append('applicationNo',applicationNo);
        data.append('status', status);
        data.append('nameOfAgency', nameOfAgency);
        data.append('proof', formProof);

        console.log(data);

        if(opr === 'update'){
            data.append('_id', _id);
            if(monthAndYear?.getMonth){
                data.append('monthAndYear',String(months[monthAndYear?.getMonth()] + " " + monthAndYear?.getFullYear()));
            } else {
                data.append('monthAndYear', monthAndYear);
            }
        } else {
            data.append('monthAndYear',String(months[monthAndYear?.getMonth()] + " " + monthAndYear?.getFullYear()));
        }


        let response;

        if(opr === 'add'){
            response = await addPatentCopyright(openForm['type'], data);
        } else {
            response = await updatePatentCopyright(openForm['type'], data);
        }
    
        alert(` Message : ${response.message}`);

        if(openForm['type'] === "Patent"){
            await handleGetPatentPaper();
        }
        if(openForm['type'] === "Copyright"){
            await handleGetCopyrightPaper();
        }

        setOpenDialog(false);

    }

    

    const finalObject = {
        openDialog : openDialog, 
        setOpenDialog : setOpenDialog,
        formProof : formProof,
        setFormProof : setFormProof, 
        activeStep : activeStep,
        setActiveStep : setActiveStep, 
        openForm : openForm, 
        setOpenForm : setOpenForm,
        formState : formState,
        setFormState : setFormState, 
        handleSubmitForm : handleSubmitForm, 
        nameOfInventors : nameOfInventors, 
        setNameOfInventors : setNameOfInventors, 
        title : title, 
        setTitle : setTitle, 
        applicationNo : applicationNo, 
        setApplicationNo : setApplicationNo, 
        monthAndYear : monthAndYear, 
        setMonthAndYear : setMonthAndYear, 
        status : status, 
        setStatus : setStatus, 
        nameOfAgency : nameOfAgency, 
        setNameOfAgency : setNameOfAgency,
        preview : previewMode,
        setOpr : setOpr, 
        _id : _id,
        set_Id : set_Id
    };

    return (
        <GlobalPCStateContext.Provider value={finalObject}>
            {children}
        </GlobalPCStateContext.Provider>
    );
}

export {GlobalPCStateContext, GlobalPCStateProvider};