import React, { useContext } from 'react';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import FormControl from '@mui/material/FormControl';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormGroup from '@mui/material/FormGroup';
import Checkbox from '@mui/material/Checkbox';
import { GlobalJCDrawerContext } from '../../features/States/JCDrawerState';
import { connect } from 'react-redux';

 function CheckBox(props) {

    const { state, handleChange } = useContext(GlobalJCDrawerContext);

    return (
        <List>
            <ListItem>
                <ListItemIcon>
                    <strong>APPROVED STATUS</strong>
                </ListItemIcon>
                <ListItemText />
            </ListItem>
            <ListItem>
                <ListItemIcon>
                    <FormControl component="fieldset">
                        <FormGroup>
                            <FormControlLabel control={
                                <Checkbox
                                    checked={(state['status'] === "Approved" ? true : false)}
                                    onChange={(e) => handleChange('status', e.target.name)}
                                    name={(state['status'] === "Approved" ? "" : "Approved")}
                                    color="primary"
                                    id="approved"
                                />
                            }
                                label="Approved"
                            />
                            <FormControlLabel control={
                                <Checkbox
                                    checked={(state['status'] === "Pending" ? true : false)}
                                    onChange={(e) => handleChange('status', e.target.name)}
                                    name={(state['status'] === "Pending" ? "" : "Pending")}
                                    color="primary"
                                    id="pendingApproval"
                                />
                            }
                                label="Pending Approval"
                            />
                            <FormControlLabel control={
                                <Checkbox
                                    checked={(state['status'] === "DisApproved" ? true : false)}
                                    onChange={(e) => handleChange('status', e.target.name)}
                                    name={(state['status'] === "DisApproved" ? "" : "DisApproved")}
                                    color="primary"
                                    id="disapproved"
                                />
                            }
                                label="Disapproved"
                            />
                        </FormGroup>
                    </FormControl>
                </ListItemIcon>
                <ListItemText />
            </ListItem>
            <ListItem>
                <ListItemIcon>
                    <strong>SHORT BY INDEX</strong>
                </ListItemIcon>
                <ListItemText />
            </ListItem>
            <ListItem>
                <ListItemIcon>
                    <FormControl component="fieldset" >
                        <FormGroup>
                            <FormControlLabel control={
                                <Checkbox
                                    checked={state['sciIndexed']}
                                    onChange={(e) => handleChange(e.target.name, e.target.checked)}
                                    name="sciIndexed"
                                    color="primary"
                                    id="sciIndexed"
                                />
                            }
                                label="SCI Indexed"
                            />
                            <FormControlLabel control={
                                <Checkbox
                                    checked={state['scopusIndexed']}
                                    onChange={(e) => handleChange(e.target.name, e.target.checked)}
                                    name="scopusIndexed"
                                    color="primary"
                                />
                            }
                                label="Scopus Indexed"
                            />
                            <FormControlLabel control={
                                <Checkbox
                                    checked={state['googleScholar']}
                                    onChange={(e) => handleChange(e.target.name, e.target.checked)}
                                    name="googleScholar"
                                    color="primary"
                                />
                            }
                                label="Google Scholar"
                            />
                            <FormControlLabel control={
                                <Checkbox
                                    checked={state['ugcListed']}
                                    onChange={(e) => handleChange(e.target.name, e.target.checked)}
                                    name="ugcListed"
                                    color="primary"
                                    id="ugcListed"
                                />
                            }
                                label="UGC Listed"
                            />
                            <FormControlLabel control={
                                <Checkbox
                                    checked={state['pubmedIndexed']}
                                    onChange={(e) => handleChange(e.target.name, e.target.checked)}
                                    name="pubmedIndexed"
                                    color="primary"
                                    id="pubmedIndexed"
                                />
                            }
                                label="PubMed Indexed"
                            />
                            {props.user.role==="HOD"?(<FormControlLabel control={
                                <Checkbox
                                    checked={state['paperToApprove']}
                                    onChange={(e) => handleChange(e.target.name, e.target.checked)}
                                    name="paperToApprove"
                                    color="primary"
                                    id="paperToApprove"
                                />
                            }
                                label="Paper To Approve"
                            />):''}
                        </FormGroup>
                    </FormControl>
                </ListItemIcon>
                <ListItemText />
            </ListItem>
            <ListItem>
                <ListItemIcon>
                    <strong>AFFILIATION</strong>

                </ListItemIcon>
                <ListItemText />
            </ListItem>
            <ListItem>
                <ListItemIcon>
                    <FormControl component="fieldset">
                        <FormGroup>
                            <FormControlLabel control={
                                <Checkbox
                                    checked={state['mmuAffiliation']}
                                    onChange={(e) => handleChange(e.target.name, e.target.checked)}
                                    name="mmuAffiliation"
                                    color="primary"
                                    id="mmuAffiliation"
                                />
                            }
                                label="MMU Affiliation"
                            />
                            <FormControlLabel control={
                                <Checkbox
                                    checked={state['mmuStudentWork']}
                                    onChange={(e) => handleChange(e.target.name, e.target.checked)}
                                    name="mmuStudentWork"
                                    color="primary"
                                    id="mmuStudentWork"
                                />
                            }
                                label="MMU Student Work"
                            />
                            <FormControlLabel control={
                                <Checkbox
                                    checked={state['afterJoiningMMU']}
                                    onChange={(e) => handleChange(e.target.name, e.target.checked)}
                                    name="afterJoiningMMU"
                                    color="primary"
                                    id="afterJoiningMMU"
                                />
                            }
                                label="After Joining MMU"
                            />
                        </FormGroup>
                    </FormControl>
                </ListItemIcon>
                <ListItemText />
            </ListItem>
            <ListItem></ListItem>
        </List>
    );
}


const mapStateToProps = state => {
    const { user } = state;
    return {
      user: user
    };
  };

  export default connect(mapStateToProps)(CheckBox);