import React from 'react';
import { Box } from '@mui/material'


export default function Layout({ children }) {

    return (
        <div style={{ width: '100%' }}>
            <Box
                display="flex"
                alignContent="flex-start"
                p={0}
                my={5}
                marginLeft={30}
                marginRight={5}
                bgcolor="white"
                sx={{ maxWidth: 1000, height: 680, boxShadow: 3, borderRadius: "2%" }}
            >
                <Box
                    sx={{
                        width: 300,
                        height: 680,
                        backgroundColor: 'black',
                        borderTopLeftRadius: "2%",
                        borderBottomLeftRadius: "2%"
                    }}
                >
                    <div style={{ width: '340%' }}>
                        <Box
                            display="flex"
                            flexWrap="wrap"
                            alignContent="flex-start"
                            p={0}
                            my={6}
                            marginLeft={20}
                            marginRight={5}
                            bgcolor="white"
                            sx={{ width: '80%', height: 600, boxShadow: 6, borderRadius: "2%" }}
                        >
                            {children}
                        </Box>
                    </div>
                </Box>
            </Box>
        </div>
    );
}